import { css } from '@emotion/react'
import { TimerButtonWithLoading } from '@src/components/timer/TimerButtonWithLoading'
import { useToast } from '@src/components/toast/ToastProvider'
import { calculateStartDate, parseDuration } from '@src/lib/timerUtils'
import { usePomodoroSettings } from '@src/pomodoro/usePomodoroSettings'
import { usePomodoroStorageState } from '@src/pomodoro/usePomodoroStorageState'
import { usePopupContext } from '@src/shared/providers/PopupProvider'
import {
  TagsListService,
  useTagsListStorage,
} from '@src/v4/services/TagsListService'
import { TEConstraintsService } from '@src/v4/services/TEConstraintsService'
import { useTEListStorage } from '@src/v4/services/TEListService'
import { useUpdateTEMutation } from '@src/v4/services/UpdateTEService'
import { useWorkspace } from '@src/v4/services/WorkspacesListService'
import debounce from 'lodash.debounce'
import { DateTime } from 'luxon'
import * as React from 'react'

import { type TimeEntryV9 } from '@toggl/track-types'
import { ButtonNext, IconStop } from '@toggl/ui'

import { DateTimeInput } from '../dateTimeInput/DateTimeInput'
import Dropdown from '../Dropdown'
import { UnmetConstraintsTooltip } from '../UnmetConstraintsTooltip'

import {
  HeaderContainer,
  RunningTimerDescription,
  TimerContainer,
} from './styles'
import TimeEntryDescription from './TimeEntryDescription'
import { TimerDropdowns } from './TimerDropdowns'
import { TimerDuration } from './TimerDuration'

export function RunningTimer({
  disableAutocomplete,
  current,
  ...props
}: {
  disableAutocomplete?: boolean
  current: TimeEntryV9
}) {
  const toast = useToast()
  const [tags] = useTagsListStorage()
  const stopButtonRef = React.useRef(null)
  const { mutate } = useUpdateTEMutation()
  const [dropdownOpen, setDropdownOpen] = React.useState(false)
  const [isStoppingTE, setIsStoppingTE] = React.useState(false)
  const [, setTimeEntries] = useTEListStorage()
  const stateOfPomodoro = usePomodoroStorageState()

  const [description, setDescription] = React.useState(current.description)

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const debouncedMutate = React.useCallback(debounce(mutate, 500), [mutate])

  // Use the missing constraints to show the tooltip.
  const ws = useWorkspace(current.workspace_id)

  React.useEffect(() => {
    if (current?.description !== description) {
      setDescription(current.description)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [current.description])

  const missingProps = TEConstraintsService.check(
    ws?.te_constraints ?? null,
    current
  )

  const [settings] = usePomodoroSettings()

  const { setActiveView } = usePopupContext()

  React.useEffect(() => {
    let hasInvalidTag = false
    if (current?.tag_ids && tags) {
      current.tag_ids.forEach((tag) => {
        if (tags[tag]?.workspace_id !== current.workspace_id)
          hasInvalidTag = true
      })
    }

    // Unsure about this behavior... need to check with the team.
    if (hasInvalidTag) {
      void TagsListService.dispatch()
    }
  }, [current?.tag_ids, current?.workspace_id, tags])

  const handleStopClick: React.MouseEventHandler = React.useCallback(
    (e) => {
      e.preventDefault()
      e.stopPropagation()

      const isMissingProps =
        missingProps.length &&
        ws?.te_constraints?.time_entry_constraints_enabled

      if (!isMissingProps) {
        setIsStoppingTE(true)
        const stopTime = new Date()
        const startTime = new Date(current.start)

        mutate(
          {
            entry: {
              ...current,
              stop: stopTime.toISOString(),
              duration: Math.floor(
                (stopTime.getTime() - startTime.getTime()) / 1000
              ),
            },
            integration: { type: 'popup' },
          },
          {
            onSuccess: (entry) => {
              setTimeEntries((prev) => ({
                ...prev,
                [entry.id]: entry,
              }))
            },
            onError: () => {
              setIsStoppingTE(false)
            },
          }
        )
      }
    },
    [
      current,
      missingProps.length,
      mutate,
      setTimeEntries,
      ws?.te_constraints?.time_entry_constraints_enabled,
    ]
  )

  const handleDatesChange = (start: string) => {
    mutate(
      {
        entry: {
          ...current,
          start,
        },
        integration: { type: 'popup' },
      },
      {
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  const updateDuration = (e: React.FocusEvent<HTMLInputElement, Element>) => {
    const start =
      calculateStartDate(parseDuration(e.target.value).toMillis() / 1000) ?? ''

    const duration = -parseInt(
      (DateTime.fromISO(start).toMillis() / 1000).toFixed(0),
      10
    )

    mutate(
      {
        entry: {
          ...current,
          start,
          duration,
        },
        integration: { type: 'popup' },
      },
      {
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  const handleDescriptionChange = (value: string) => {
    setDescription(value)

    debouncedMutate(
      {
        entry: {
          ...current,
          description: value,
        },
        integration: { type: 'popup' },
      },
      {
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  const handleDropdownsChange = ({
    billable,
    selectedTask,
    selectedProject,
    selectedTags,
  }: {
    billable: boolean
    selectedTask?: number | undefined
    selectedProject?: number | null | undefined
    selectedTags?: number[] | null | undefined
  }) => {
    mutate(
      {
        entry: {
          ...current,
          billable,
          task_id: selectedTask,
          project_id: selectedProject,
          tag_ids: selectedTags,
        },
        integration: { type: 'popup' },
      },
      {
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  const handleAutocompleteChange = (te: TimeEntryV9, e: Event) => {
    e.preventDefault()

    mutate(
      {
        entry: {
          ...current,
          project_id: te.project_id,
          task_id: te.task_id,
          tag_ids: te.tag_ids,
          billable: te.billable,
          description: te.description,
        },
        integration: { type: 'popup' },
      },
      {
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  return (
    <HeaderContainer {...props}>
      <TimerContainer>
        {settings?.isEnabled && (
          <ButtonNext
            variant='tertiary'
            type='button'
            size='regular'
            content='text'
            css={{
              fontSize: 24,
              padding: 0,
              aspectRatio: 1,
              borderRadius: '50%',
              marginRight: 8,
            }}
            onClick={() =>
              setActiveView((current) =>
                current === 'LIST' ? 'POMODORO' : 'LIST'
              )
            }
          >
            <img
              src='/images/pomodoro-icon.png'
              alt='Pomodoro Icon'
              height={28}
            />
          </ButtonNext>
        )}
        <RunningTimerDescription>
          <TimeEntryDescription
            running
            description={description}
            onChange={handleDescriptionChange}
            onSelectTimeEntry={handleAutocompleteChange}
            disableAutocomplete={disableAutocomplete}
          />
        </RunningTimerDescription>
        <div
          ref={stopButtonRef}
          css={css`
            position: relative;
          `}
        >
          {current?.start && (
            <Dropdown>
              <Dropdown.RawTrigger onFocus={() => setDropdownOpen(true)}>
                <TimerDuration
                  disabled={!!stateOfPomodoro.isRunning}
                  start={current.start}
                  updateDuration={updateDuration}
                  setDropdownOpen={setDropdownOpen}
                />
              </Dropdown.RawTrigger>
              <Dropdown.Overlay />
              <Dropdown.DateTimeContent
                open={dropdownOpen}
                position={{ left: -192, top: 40 }}
              >
                <DateTimeInput
                  start={current.start ?? DateTime.now().toISO()}
                  stop={DateTime.now().toFormat('HH:mm')}
                  onDatesChange={handleDatesChange}
                  disableStop
                  disableManualCreation
                />
              </Dropdown.DateTimeContent>
            </Dropdown>
          )}
          <UnmetConstraintsTooltip
            enabled={!!missingProps?.length}
            missingProps={missingProps}
          >
            <div>
              <TimerButtonWithLoading
                isStop
                loading={isStoppingTE}
                onClick={handleStopClick}
                icon={<IconStop />}
              />
            </div>
          </UnmetConstraintsTooltip>
        </div>
      </TimerContainer>
      <TimerDropdowns
        onDropdownsChange={handleDropdownsChange}
        timeEntry={current}
      />
    </HeaderContainer>
  )
}
