import styled from '@emotion/styled'

import styles from '@toggl/style'
import { ArrowDownIconNext, ButtonNext } from '@toggl/ui'

type Props = {
  workspaceName: string
  isOpen?: boolean
  onOpenChange: () => void
}

export function WorkSpaceTrigger(props: Props) {
  return (
    <ButtonNext
      content='icon-right'
      size='regular'
      variant='tertiary'
      active={props.isOpen}
      onClick={props.onOpenChange}
      icon={<ArrowDownIconNext />}
    >
      <Title>{props.workspaceName}</Title>
    </ButtonNext>
  )
}

const Title = styled.h1`
  ${styles.typography.h5Subtitle}
  white-space: nowrap;
  overflow: hidden;
  max-width: 250px;
  text-overflow: ellipsis;
  margin: 0;
`
