import styled from '@emotion/styled'
import * as Sentry from '@sentry/browser'
import Pomodoro from '@src/components/Pomodoro'
import Summary from '@src/components/Summary'
import { TEsListOnboarding } from '@src/components/tesListOnboarding'
import TimeEntriesList from '@src/components/TimeEntriesList'
import { Timer } from '@src/components/timer/Timer'
import { useExtensionContext } from '@src/context'
import useSetting from '@src/lib/hooks/useSetting'
import { calculateSums } from '@src/lib/timerUtils'
import { useOnboardingContext } from '@src/onboarding/OnboardingProvider'
import { LoadingOverlay } from '@src/shared/components/LoadingOverlay'
import { usePopupContext } from '@src/shared/providers/PopupProvider'
import { getGroupedTEs } from '@src/shared/utils/getGroupedTEs'
import { Button } from '@src/ui/components'
import { useProjectsListStorage } from '@src/v4/services/ProjectsListService'
import { useTEListStorage } from '@src/v4/services/TEListService'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

const messages = defineMessages({
  surveyCTA: {
    id: 'home.surveyCTA',
    defaultMessage: 'Take a quick survey!',
  },
})

const Home: React.FC = () => {
  const { user } = useExtensionContext()
  const [projects] = useProjectsListStorage()
  const { currentStep } = useOnboardingContext()
  const [surveyTaken, setSurveyTaken] = useSetting('surveyTaken')

  const { activeView } = usePopupContext()

  const [timeEntries, , { isCacheLoading }] = useTEListStorage()

  const elements = React.useMemo(
    () => getGroupedTEs(Object.values(timeEntries ?? {})),
    [timeEntries]
  )

  React.useEffect(() => {
    Sentry.setUser({ id: String(user?.id) })
  }, [user?.id])

  const TEsList = Object.values(timeEntries ?? {})

  if (isCacheLoading) {
    return <LoadingOverlay />
  }

  const isPomodoroView = activeView === 'POMODORO'
  const isListView = activeView === 'LIST'

  return (
    <Root id='homeRoot'>
      {!!TEsList.length && <Timer />}

      {currentStep !== 'finished' && !isPomodoroView && !!TEsList && (
        <TEsListOnboarding
          showImg={!TEsList.length || currentStep === 'initial'}
        />
      )}

      {currentStep !== 'initial' && (
        <React.Fragment>
          {TEsList.length > 0 && user && (
            <Summary totals={calculateSums(TEsList, user.beginning_of_week)} />
          )}

          {isPomodoroView && <Pomodoro />}

          {isListView && timeEntries && projects && (
            <TEListWrapper>
              <TimeEntriesList data={elements} projects={projects} />

              {!surveyTaken && !!elements?.length && (
                <SurveyButtonContainer>
                  <Anchor
                    href='https://toggl.typeform.com/to/sI2cCiTE'
                    target='_blank'
                    rel='noreferrer'
                  >
                    <StyledButton
                      onClick={() => {
                        setSurveyTaken(true)
                      }}
                    >
                      <FormattedMessage {...messages.surveyCTA} />
                    </StyledButton>
                  </Anchor>
                </SurveyButtonContainer>
              )}
            </TEListWrapper>
          )}
        </React.Fragment>
      )}
    </Root>
  )
}

const Root = styled.div`
  display: contents;
  background-color: ${({ theme }) => theme.colors.surface_secondary.regular};
`

const TEListWrapper = styled.div`
  flex: 1;
  overflow-y: auto;
  background-color: ${({ theme }) => theme.colors.surface_secondary.regular};
  // Enforce hide persistent scrollbar in all browsers
  scrollbar-width: none;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
  }
`

const SurveyButtonContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
  margin-top: 16px;
`

const Anchor = styled.a`
  width: 100%;
  display: flex;
  justify-content: center;
  text-decoration: none;
`

const StyledButton = styled(Button)`
  width: fit-content;
  color: ${({ theme }) => theme.colors.accent.regular};
  border: none;
  font-weight: 500;
  text-decoration: none;
`

export default Home
