import { css } from '@emotion/react'
import styled from '@emotion/styled'
import { AppThemeProvider } from '@src/components/AppThemeProvider'
import Header from '@src/components/Header'
import { ExtensionContext, useContextState } from '@src/context'
import '@src/lib/heap'
import { abTester } from '@src/lib/abTester'
import { useFullFetchStatus } from '@src/lib/full-fetch'
import { useReminderNotificationExperiment } from '@src/lib/hooks/useReminderNotificationExperiment'
import { getOriginsForBuild } from '@src/lib/utils'
import { useOnboardingContext } from '@src/onboarding/OnboardingProvider'
import { LoadingOverlay } from '@src/shared/components/LoadingOverlay'
import { PopupProvider } from '@src/shared/providers/PopupProvider'
import { TEListService } from '@src/v4/services/TEListService'
import { useUserStorage } from '@src/v4/services/UsersService'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import Browser from 'webextension-polyfill'

import styles from '@toggl/style'
import { ArrowLeftIconNext, ButtonNext } from '@toggl/ui'

import { EditTimeEntry } from '../editTimeEntry/EditTimeEntry'
import Home from '../home/Home'
import { LoginPage } from '../login/Login'
import { NoPermission } from '../noPermission'

const messages = defineMessages({
  back: {
    id: 'popup.back',
    defaultMessage: 'Back',
  },
})

const usePermissionsCheck = () => {
  const [isLoading, setIsLoading] = React.useState(true)
  const [isAllowed, setIsAllowed] = React.useState(true)

  React.useEffect(() => {
    Browser.permissions
      .contains({
        origins: getOriginsForBuild(),
      })
      .then((res) => setIsAllowed(res))
      .catch(() => setIsAllowed(false))
      .finally(() => setIsLoading(false))
  }, [])

  return { isLoading, isAllowed }
}

const useTEListDispatch = () => {
  const [isLoading, setIsLoading] = React.useState(true)
  const [isError, setIsError] = React.useState(true)

  React.useEffect(() => {
    TEListService.dispatch()
      .catch(() => setIsError(true))
      .finally(() => setIsLoading(false))
  }, [])

  return { isLoading, isError }
}

const Popup: React.FC = () => {
  const [fullFetch] = useFullFetchStatus()
  const state = useContextState()
  const [user] = useUserStorage()

  // to update the setting when popup is opened
  useReminderNotificationExperiment()

  const { isLoading: isLoadingTEList } = useTEListDispatch()
  const { isLoading: isLoadingOnboarding } = useOnboardingContext()
  const { isLoading: isLoadingPermissions, isAllowed } = usePermissionsCheck()

  React.useEffect(() => {
    if (user) {
      if (window.heap) window.heap.identify(`${user.id}`)
      abTester.setAttributes({ id: user.id })
    }
  }, [user])

  const isLoading =
    isLoadingTEList ||
    isLoadingPermissions ||
    isLoadingOnboarding ||
    state?.loading

  const isEditing = state?.view === 'editEntry'

  const handleBack = (e) => {
    e.preventDefault()

    state.changeView('default')
    state.changeEditingEntry(undefined)
  }

  return (
    <AppThemeProvider injectCSSVarAsGlobal>
      <PopupProvider>
        {isLoading && <LoadingOverlay />}
        {!isLoading && !isAllowed && <NoPermission />}
        {!isLoading && isAllowed && !state?.user && (
          <React.Fragment>
            <Header />
            {fullFetch?.loading ? <LoadingOverlay /> : <LoginPage />}
          </React.Fragment>
        )}
        {!isLoading && isAllowed && state?.user && (
          <ExtensionContext.Provider value={state}>
            <Header />

            {isEditing ? (
              <EditEntryContainer>
                <ButtonNext
                  onClick={handleBack}
                  css={css`
                    align-self: flex-start;
                    margin-left: ${styles.spacings['5x']};
                    margin-top: ${styles.spacings['2x']};
                    margin-bottom: ${styles.spacings['1x']};
                  `}
                  content='icon-left'
                  size='regular'
                  variant='tertiary'
                  icon={<ArrowLeftIconNext color='ui_050' />}
                >
                  <FormattedMessage {...messages.back} />
                </ButtonNext>
                <EditTimeEntry
                  fromIntegration={{ type: 'popup' }}
                  projectPopdownPosition={{ left: -40, top: 40 }}
                  tagPopdownPosition={{ left: -40, top: 40 }}
                  durationPopdownPosition={{ left: -21, top: 33, width: 342 }}
                />
              </EditEntryContainer>
            ) : (
              <Home />
            )}
          </ExtensionContext.Provider>
        )}
      </PopupProvider>
    </AppThemeProvider>
  )
}

export default Popup

const EditEntryContainer = styled.div`
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: ${styles.colors.surface_secondary.regular};
  position: relative;
`
