import * as React from 'react'

import { type CurrentUser, type TimeEntryV9 } from '@toggl/track-types'

export { default as useContextState } from './state'

export type ViewType = 'default' | 'editEntry'

export type ExtensionContextValue = {
  loading: boolean
  view: ViewType
  user: CurrentUser | null
  editingEntry: TimeEntryV9 | undefined
  changeView: (value: ViewType) => void
  changeEditingEntry: (value: TimeEntryV9 | undefined) => void
}

const initialState: ExtensionContextValue = {
  loading: false,
  view: 'default',
  user: null,
  editingEntry: undefined,
  changeView: (value: ViewType) => value,
  changeEditingEntry: (value: TimeEntryV9 | undefined) => value,
}

export const ExtensionContext: React.Context<ExtensionContextValue> =
  React.createContext(initialState)

export const useExtensionContext = (): ExtensionContextValue =>
  React.useContext(ExtensionContext)
