import { useVirtualized } from '@src/lib/hooks/useVirtualized'
import keycode from 'keycode'
import * as React from 'react'

import { type Tag } from '@toggl/track-types'

type Props = {
  items: Tag[]
  onSelectItem: (item: Tag) => void
  onClose: () => void
  defaultFocusedIndex?: number
  searchInputRef?: React.RefObject<any>
}
// This would ensure nothing is selected but pressing down key would focus first item
const NO_SELECTION = -1

export function useVirtualizedTagList({
  items,
  onSelectItem,
  onClose,
  defaultFocusedIndex = NO_SELECTION,
  searchInputRef,
}: Props) {
  const focusIndex = React.useRef<number>(defaultFocusedIndex)
  const {
    scrollToIndex,
    setScrollToIndex,
    isVirtualIndexRendered,
    ensureVirtualIndex,
    onVirtualRowsRendered,
  } = useVirtualized()

  const focusItem = React.useCallback(
    (index: number) => {
      focusIndex.current = index
      if (isVirtualIndexRendered(index)) {
        setScrollToIndex(index)
      } else {
        ensureVirtualIndex(focusIndex.current)
      }
    },
    [ensureVirtualIndex, isVirtualIndexRendered, setScrollToIndex]
  )

  // Focus first item after filter/list changes
  React.useEffect(() => {
    if (items.length > 0 && focusIndex.current !== defaultFocusedIndex) {
      focusIndex.current = defaultFocusedIndex
    }
  }, [defaultFocusedIndex, items.length])

  const moveFocus = React.useCallback(
    (step: number) => {
      if (items.length === 0) {
        return
      }
      const index = focusIndex.current || 0
      const nextFocusIndex = (items.length + index + step) % items.length

      focusItem(nextFocusIndex)
    },
    [items.length, focusItem]
  )

  const onKeyDown = React.useCallback(
    (e: React.KeyboardEvent<HTMLDivElement>) => {
      const key = e.which
      const down = key === keycode('down')
      const up = key === keycode('up')
      const spaceOrEnter = key === keycode('enter')

      if (key === keycode('esc')) {
        onClose()
      } else if (down || up) {
        e.preventDefault()
        moveFocus(down ? 1 : -1)
      } else if (focusIndex.current != null && spaceOrEnter) {
        const item = items[focusIndex.current]
        e.preventDefault()
        onSelectItem(item)

        if (key === keycode('enter')) {
          // Need to stop propagation to prevent this popdown from closing before
          // item is selected
          e.stopPropagation()
        }
      }
    },
    [items, moveFocus, onClose, onSelectItem]
  )

  return React.useMemo(
    () => ({
      scrollToIndex,
      onVirtualRowsRendered,
      moveFocus,
      focusIndex,
      onKeyDownHandler: onKeyDown,
    }),
    [moveFocus, onVirtualRowsRendered, scrollToIndex, onKeyDown]
  )
}
