import { css } from '@emotion/react'
import styled from '@emotion/styled'
import { useToast } from '@src/components/toast/ToastProvider'
import { useSelectedWorkspaceIdStorage } from '@src/v4/services/SelectedWorkspaceService'
import { useUserStorage } from '@src/v4/services/UsersService'
import { useUserUpdateMutation } from '@src/v4/services/UserUpdateService'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import styles from '@toggl/style'
import { type Workspace } from '@toggl/track-types'
import {
  AnimatedLoadingIcon,
  CheckmarkIconNext,
  StateBadgeNext,
  WorkspaceIconNext,
} from '@toggl/ui'

type Props = {
  workspace: Workspace
}

const messages = defineMessages({
  default: {
    id: 'workspaceItem.default',
    defaultMessage: 'Default',
  },
  setDefault: {
    id: 'workspaceItem.setDefault',
    defaultMessage: 'Set as default',
  },
})

const DEFAULT_WORKSPACE_LOGO =
  'https://assets.track.toggl.com/images/workspace.jpg' as const

export function WorkspaceListItem({ workspace }: Props) {
  const [user] = useUserStorage()
  const [selectedWS, setSelectedWS] = useSelectedWorkspaceIdStorage()
  const mutation = useUserUpdateMutation()
  const toast = useToast()
  const isSelected = selectedWS === workspace.id
  const isDefault = user?.default_workspace_id === workspace.id

  const handleChangeDefault = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    if (isDefault) return
    e.preventDefault()
    e.stopPropagation()
    mutation.mutate(
      { default_workspace_id: workspace.id },
      {
        onError: (error) => {
          toast.addToast({
            type: 'error',
            title: 'Error updating default workspace',
            message: error.message,
          })
        },
      }
    )
  }

  const handleSelectWorkspace = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault()
    e.stopPropagation()

    setSelectedWS(workspace.id)
  }

  return (
    <Li>
      <WorkspaceItem
        data-workspace-selected={isSelected}
        onClick={handleSelectWorkspace}
      >
        <ItemLabelContainer>
          <StyledCheckMark
            data-selected={isSelected}
            size='2x'
            color='accent'
          />
          {workspace.logo_url === DEFAULT_WORKSPACE_LOGO ? (
            <WorkspaceIconNext color='ui_040' />
          ) : (
            <img
              css={css`
                display: block;
              `}
              height={20}
              src={workspace.logo_url}
              alt={workspace.name}
            />
          )}
          <ItemName>{workspace.name}</ItemName>
        </ItemLabelContainer>
        {isDefault ? (
          <StateBadgeNext
            type='info'
            size='xs'
            css={css`
              letter-spacing: 1px;
              font-weight: 500;
            `}
          >
            <FormattedMessage {...messages.default} />
          </StateBadgeNext>
        ) : (
          <WorkspaceCta
            onClick={handleChangeDefault}
            disabled={mutation.isLoading}
          >
            {mutation.isLoading ? (
              <AnimatedLoadingIcon />
            ) : (
              <FormattedMessage {...messages.setDefault} />
            )}
          </WorkspaceCta>
        )}
      </WorkspaceItem>
    </Li>
  )
}

const Li = styled.li`
  list-style: none;
`

const WorkspaceItem = styled.button`
  ${styles.unset.button};
  ${styles.text.nextP1Body};

  width: 100%;
  padding: ${styles.spacings['1.5x']} ${styles.spacings['2x']};

  display: flex;
  justify-content: space-between;
  align-items: center;

  cursor: pointer;

  transition: background-color 0.15s ease-in;

  &[data-workspace-selected='true'] {
    font-weight: ${styles.text.fontWeight.bold};
  }

  :hover,
  :focus-visible,
  :focus-within {
    transition: background-color 0.1s ease-out;
    background-color: ${styles.colors.surface_tertiary.hover};
  }
`

const WorkspaceCta = styled.button`
  ${styles.unset.button};
  ${styles.text.nextH6SubtitleCaps};

  flex-shrink: 0;

  color: ${styles.colors.accent_120.regular};
  padding: ${styles.spacings['0.25x']} ${styles.spacings['0.5x']};
  border-radius: ${styles.radii.regular};

  cursor: pointer;

  opacity: 0;
  transition: opacity 0.15s ease-out, color 0.1s ease-out;

  .WorkspaceItem:hover &,
  .WorkspaceItem:focus-visible & {
    transition: opacity 0.1s ease-out 0.3s, color 0.1s ease-out,
      background-color 0.1s ease-out;

    opacity: 1;
  }

  :hover,
  :focus-visible {
    opacity: 1;
    color: ${styles.colors.accent_120.hover};
  }

  :hover {
    transition: opacity 0.025s ease-out, color 0.1s ease-out;
  }

  :focus-visible {
    transition: opacity 0.15s ease-out, background-color 0.1s ease-out,
      color 0.15s ease-out;

    opacity: 1;
    color: ${styles.colors.accent_120.onClick};
    background-color: ${styles.colors.accent_020.onClick};
  }
`

const ItemName = styled.span`
  ${styles.text.noOverflow};
`

const ItemLabelContainer = styled.span`
  display: inline-flex;

  align-items: center;
  gap: ${styles.spacings['1x']};

  overflow: hidden;

  max-width: 275px;

  > svg {
    flex-shrink: 0;
  }
`

const StyledCheckMark = styled(CheckmarkIconNext)`
  opacity: 0;
  filter: blur(2px);
  transition: opacity 0.15s ease-in, filter 0.15s ease-in;

  &[data-selected='true'] {
    opacity: 1;
    filter: blur(0);
    transition: opacity 0.25s ease-out, filter 0.25s ease-out;
  }
`
