import { css } from '@emotion/react'
import styled from '@emotion/styled'
import { Time, parseTime } from '@internationalized/date'
import { FieldWrapper } from '@src/ui/components'
import moment from 'moment'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import { type TimeEntryV9 } from '@toggl/track-types'
import { ButtonNext, IconAdd } from '@toggl/ui'

import DayPicker from './DayPicker'
import { StartTimeInput } from './StartTimeInput'
import { StopTimeInput } from './StopTimeInput'

const messages = defineMessages({
  start: {
    id: 'DateTimeInput.start',
    defaultMessage: 'Start',
  },
  stop: {
    id: 'DateTimeInput.stop',
    defaultMessage: 'Stop',
  },
})

type Props = {
  start?: string
  stop?: string | null
  onDatesChange: (start: string, stop: string) => void
  disableStop?: boolean
  createEntry?: (e: any) => Promise<TimeEntryV9> | undefined
  disableManualCreation?: boolean
}

export const DateTimeInput: React.FC<Props> = (props) => {
  const [isCreatingTE, setIsCreatingTE] = React.useState(false)

  const [start, setStart] = React.useState(
    props.start ? parseTime(moment(props.start).format('HH:mm')) : new Time()
  )
  const [stop, setStop] = React.useState<Time | undefined>(
    props.stop ? parseTime(props.stop) : new Time()
  )
  const [date, setDate] = React.useState(moment(props.start) ?? moment())

  React.useEffect(() => {
    setStart(
      props.start ? parseTime(moment(props.start).format('HH:mm')) : new Time()
    )
    setStop(props.stop ? parseTime(props.stop) : new Time())
  }, [props.start, props.stop])

  const notifyDatesChange = ({
    start,
    stop,
    date,
  }: {
    start: Time
    stop?: Time
    date: moment.Moment
  }) => {
    const startDateTime = date.clone().set({
      hour: start.hour,
      minute: start.minute,
    })
    const stopDateTime = date.clone().set({
      hour: stop?.hour,
      minute: stop?.minute,
    })

    if (stopDateTime.isBefore(startDateTime)) {
      props.onDatesChange(
        startDateTime.toISOString(),
        stopDateTime.add(1, 'day').toISOString()
      )
    } else {
      props.onDatesChange(
        startDateTime.toISOString(),
        stopDateTime.toISOString()
      )
    }
  }

  const handleStartChange = (value: Time) => {
    setStart(value)
    notifyDatesChange({ start: value, stop, date })
  }

  const handleStopChange = (value: Time) => {
    setStop(value)
    notifyDatesChange({ start, stop: value, date })
  }

  const handleDateChange = (date: moment.Moment) => {
    setDate(date)
    notifyDatesChange({ start, stop, date })
  }
  const { createEntry } = props

  const handleCreateTE = React.useCallback(
    (e) => {
      if (createEntry) {
        setIsCreatingTE(true)
        void createEntry?.(e)
          // We don't care about it yet
          ?.catch()
          .finally(() => setIsCreatingTE(false))
      }
    },
    [createEntry]
  )

  return (
    <Root>
      <TimeInputsContainer>
        <StyledFieldWrapper>
          <label>
            <FormattedMessage {...messages.start} />
          </label>
          <StartTimeInput
            date={date}
            onChange={handleStartChange}
            value={start}
          />
        </StyledFieldWrapper>
        <StyledFieldWrapper>
          <label>
            <FormattedMessage {...messages.stop} />
          </label>
          <StopTimeInput
            value={stop}
            onChange={handleStopChange}
            leapingDays={stop && stop < start ? 1 : undefined}
            disabled={props.disableStop}
          />
        </StyledFieldWrapper>
        {!props.disableManualCreation && (
          <div
            css={css`
              display: flex;
              align-items: center;
            `}
          >
            <ButtonNext
              loading={isCreatingTE}
              onClick={handleCreateTE}
              content='icon-only'
              size='regular'
              variant='tertiary'
              icon={<IconAdd size={18} />}
            />
          </div>
        )}
      </TimeInputsContainer>
      <DayPicker
        firstDayOfWeek={1}
        withKeyboardNavigation
        date={date}
        onDateChange={handleDateChange}
      />
    </Root>
  )
}

const Root = styled.div`
  display: flex;
  flex-direction: column;
  width: fit-content;
  background-color: ${({ theme }) => theme.colors.surface_primary.regular};
  box-shadow: 0px 2px 16px rgba(44, 19, 56, 0.3);
  border: 1px solid ${({ theme }) => theme.colors.ui_010.regular};
  border-radius: 8px;
`

const TimeInputsContainer = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 16px;
  gap: 16px;
`

const StyledFieldWrapper = styled(FieldWrapper)`
  display: flex;
  flex-direction: column;
  width: 100%;

  label {
    margin-bottom: 4px;
    font-size: 11px;
    text-transform: uppercase;
    color: ${({ theme }) => theme.colors.ui_060.regular};
    font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  }
`
