import { type PomodoroState } from '@src/pomodoro/PomodoroService'
import { getRemainingSessionTime } from '@src/pomodoro/utils/getRemainingSessionTime'
import * as React from 'react'

export const usePomodoroInterval = (state: PomodoroState) => {
  const [remaining, setRemaining] = React.useState(state.duration)

  React.useEffect(() => {
    let interval: NodeJS.Timer | undefined

    if (!state) {
      return
    }

    if (!state.isRunning) {
      setRemaining(state.duration)
      clearInterval(interval)
    }

    if (state.isRunning) {
      // Immediately update the remaining time
      setRemaining(getRemainingSessionTime(state.isRunning, state.duration))

      interval = setInterval(
        () =>
          setRemaining(
            getRemainingSessionTime(state.isRunning as number, state.duration)
          ),
        1000
      )
    }

    return () => clearInterval(interval)
  }, [state, state?.duration, state?.isRunning])

  return remaining
}
