import * as React from 'react'

import styles from '@toggl/style'

const getTickMarks = () => {
  const marks: JSX.Element[] = []

  for (let i = 0; i < 48; i++) {
    const isMajorTick = i % 4 === 0
    const tickLength = 6 // Same length for all ticks
    const strokeWidth = isMajorTick ? 2 : 1

    const strokeColor = isMajorTick
      ? styles.colors.ui.regular
      : styles.colors.ui_040.regular

    marks.push(
      <path
        key={i}
        d={`M150 150 m0 -120 v${tickLength}`}
        stroke={strokeColor}
        strokeWidth={strokeWidth}
        transform={`rotate(${i * 7.5} 150 150)`}
      />
    )
  }

  return marks
}

const getArcProgressPath = (progress: number, color: string) => {
  const endAngle = 360 * (progress / 100)
  const x = 150 + 140 * Math.sin((Math.PI * endAngle) / 180)
  const y = 150 - 140 * Math.cos((Math.PI * endAngle) / 180)
  const largeArcFlag = endAngle > 180 ? 1 : 0

  if (progress === 100) {
    return (
      <path
        d='M150 10 A140 140 0 1 1 150 290 A140 140 0 1 1 150 10'
        fill='none'
        stroke={color}
        strokeWidth='20'
        strokeLinecap='round'
      />
    )
  }

  return (
    <path
      d={`M150 10 A140 140 0 ${largeArcFlag} 1 ${x} ${y}`}
      fill='none'
      stroke={color}
      strokeWidth='20'
      strokeLinecap='round'
    />
  )
}

type Props = React.PropsWithChildren<{
  label: string
  progress: number
  opts?: {
    color: string
  }
}>

export function CircularTimer({
  label,
  children,
  progress,
  opts = { color: styles.colors.ui_020.regular },
}: Props) {
  const clockTicks = React.useMemo(() => getTickMarks(), [])

  // Calculate the end angle of the arc based on progress
  const arcProgressPath = getArcProgressPath(progress, opts.color)

  return (
    <svg
      width='100%'
      height='100%'
      viewBox='0 0 300 300'
      preserveAspectRatio='xMidYMid meet'
      xmlns='http://www.w3.org/2000/svg'
    >
      {/* Circle background */}
      <circle
        cx='150'
        cy='150'
        r='140'
        fill={styles.colors.surface_secondary.regular}
      />

      {/* Outer circle */}
      <circle
        cx='150'
        cy='150'
        r='140'
        fill='none'
        stroke={styles.colors.ui_040.regular}
        strokeWidth='20'
      />

      {/* Progress arc */}
      {arcProgressPath}

      {/* Tick marks */}
      <g>{clockTicks}</g>

      {/* Label Text */}
      <text
        x='150'
        y='120'
        fontSize='24'
        textAnchor='middle'
        fill={styles.colors.ui.regular}
        style={{
          fontFamily: 'GT Haptik, sans-serif',
        }}
      >
        {label}
      </text>

      {/* Inner circle */}
      <text
        x='150'
        y='180'
        fontSize='48'
        fontWeight='bold'
        textAnchor='middle'
        fill={styles.colors.ui.regular}
        style={{
          fontFamily: 'GT Haptik, sans-serif',
        }}
      >
        {children}
      </text>
    </svg>
  )
}
