import { logger } from '@src/lib/errorReport'
import { useMutation } from '@tanstack/react-query'
import Browser from 'webextension-polyfill'

import { type TimeEntryV9 } from '@toggl/track-types'

type MutationContext = TimeEntryV9

const DeleteTEService = {
  // WIP: Still using legacy event 'delete-entry' to keep logic.
  dispatch(data: MutationContext): Promise<void> {
    return Browser.runtime.sendMessage({
      type: 'delete-entry',
      payload: data,
    })
  },
}

type MutationData = void

const useDeleteTEMutation = () => {
  return useMutation<MutationData, Error, MutationContext>(
    (data) => DeleteTEService.dispatch(data),
    {
      onError: (error) => {
        return logger.error(error)
      },
    }
  )
}

export { DeleteTEService, useDeleteTEMutation }
