import styled from '@emotion/styled'
import keycode from 'keycode'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import { type TimeEntryV9 } from '@toggl/track-types'

import PreviouslyTracked from './PreviouslyTracked'
import { type TimerMode } from './Timer'

type Props = {
  running?: boolean
  timerMode?: TimerMode
  description: string
  onChange: (value: string) => void
  onBlur?: () => void
  startTimer?: (e: any) => void
  onSelectTimeEntry: (timeEntry: TimeEntryV9, e) => void
  disableAutocomplete?: boolean
}

const messages = defineMessages({
  descriptionPlaceholder: {
    id: 'timer.descriptionPlaceholder',
    defaultMessage: 'What are you working on?',
  },
  runningDescriptionPlaceholder: {
    id: 'timer.runningDescriptionPlaceholder',
    defaultMessage: '(no description)',
  },
})

function evaluateMessage(running: boolean) {
  if (running) {
    return messages.runningDescriptionPlaceholder
  }
  return messages.descriptionPlaceholder
}

const TimeEntryDescription = (
  {
    running = false,
    timerMode = 'auto',
    description,
    onChange,
    onBlur,
    startTimer,
    onSelectTimeEntry,
    disableAutocomplete,
  }: Props,
  ref: React.Ref<HTMLInputElement>
) => {
  const [showPreviously, setShowPreviously] = React.useState(false)
  const autocompleteRef = React.useRef<HTMLDivElement>(null)

  const onKeyUp: React.KeyboardEventHandler<HTMLInputElement> =
    React.useCallback(
      (e) => {
        if (keycode(e.which) === 'enter') {
          startTimer && startTimer(e)
          onBlur && onBlur()
        }
      },
      [onBlur, startTimer]
    )

  const onKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Escape') {
      e.preventDefault()
      showPreviously && setShowPreviously(false)
    } else {
      setShowPreviously(true)
    }
    if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
      e.preventDefault()
      autocompleteRef.current?.focus()
    }
  }

  const handleChange: React.ChangeEventHandler<HTMLInputElement> =
    React.useCallback(
      (e) => {
        e.preventDefault()
        e.stopPropagation()

        onChange(e.target.value)
      },
      [onChange]
    )

  const handleBlur: React.FocusEventHandler<HTMLInputElement> =
    React.useCallback(
      (e) => {
        e.preventDefault()
        e.stopPropagation()

        // setShowPreviously(false);
        onBlur && onBlur()
      },
      [onBlur]
    )

  return (
    <TimerInputWrapper>
      <FormattedMessage {...evaluateMessage(running)}>
        {(placeholder) => (
          <TimerInput
            ref={ref}
            id='timerDescription'
            value={description}
            onChange={handleChange}
            onKeyUp={onKeyUp}
            onKeyDown={onKeyDown}
            onBlur={handleBlur}
            placeholder={placeholder as unknown as string}
            onFocus={() => setShowPreviously(true)}
          />
        )}
      </FormattedMessage>
      {!disableAutocomplete && showPreviously && (
        <PreviouslyTracked
          show={showPreviously && timerMode === 'auto'}
          hide={() => setShowPreviously(false)}
          ref={autocompleteRef}
          description={description}
          onClick={onSelectTimeEntry}
        />
      )}
    </TimerInputWrapper>
  )
}

export default React.forwardRef(TimeEntryDescription)

const TimerInput = styled.input`
  flex: 1;
  height: 40px;
  border: none;

  font-size: 16px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  background: ${({ theme }) => theme.colors.surface_primary.regular};
  color: ${({ theme }) => theme.colors.ui.regular};
  margin-right: 10px;
  border-radius: 20px;

  width: 100%;
  text-overflow: ellipsis;

  &:hover,
  &:focus {
    outline: none;
  }
`

const TimerInputWrapper = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
`
