import { css } from '@emotion/react'
import { StyledTimerButton } from '@src/components/timer/styles'
import keycode from 'keycode'
import type * as React from 'react'

import styles from '@toggl/style'
import { AnimatedLoadingIcon } from '@toggl/ui'

type Props = {
  loading?: boolean
  onClick: (e: any) => void
  icon: React.ReactNode
  isStop?: boolean
}

export function TimerButtonWithLoading(props: Props) {
  return (
    <div
      css={css`
        min-height: ${styles.spacings['7x']};
        min-width: ${styles.spacings['7x']};
        display: flex;
        align-items: center;
        justify-content: center;
      `}
    >
      {props.loading ? (
        <AnimatedLoadingIcon color={styles.colors.ui.regular} />
      ) : (
        <StyledTimerButton
          isStop={props.isStop}
          onClick={props.onClick}
          tabIndex={0}
          onKeyUp={(e) => {
            if (keycode(e.which) === 'enter') props.onClick(e)
          }}
        >
          {props.icon}
        </StyledTimerButton>
      )}
    </div>
  )
}
