import { type PomodoroState } from '@src/pomodoro/PomodoroService'

import styles from '@toggl/style'

export const getPomodoroColors = (state: PomodoroState) => {
  if (!state.isRunning && !state.isBreak && !state.isLargeBreak) {
    return {
      arc: styles.colors.accent.regular,
      background: styles.colors.accent_020.regular,
    }
  }

  if (state.isBreak || state.isLargeBreak) {
    return {
      arc: styles.colors.success_120.regular,
      background: styles.colors.success_020.regular,
    }
  }

  return {
    arc: styles.colors.orange.regular,
    background: styles.colors.background_secondary.regular,
  }
}
