import { createContext } from '@src/shared/utils/createContext'
import { useBrowserStorage } from '@src/v4/lib/use-browser-storage'
import type * as React from 'react'

import { config } from './config'

export type OnboardingStep =
  | 'initial'
  | 'all_enabled'
  | 'enable_manually'
  | 'finished'

export type OnboardingContextReturn = {
  isLoading: boolean
  currentStep: OnboardingStep | null
  setCurrentStep: (step: OnboardingStep) => void
}

const [OnboardingContextProvider, useOnboardingContext] =
  createContext<OnboardingContextReturn>({
    strict: false,
    name: 'OnboardingContext',
  })

export const OnboardingProvider = ({
  children,
  ...rest
}: React.PropsWithChildren) => {
  const [currentStep, setCurrentStep, { isCacheLoading }] =
    useBrowserStorage<OnboardingStep>(config.storage, 'initial', 'sync')

  return (
    <OnboardingContextProvider
      value={{ currentStep, setCurrentStep, isLoading: isCacheLoading }}
    >
      {children}
    </OnboardingContextProvider>
  )
}

export { useOnboardingContext }
