import { AppThemeProvider } from '@src/components/AppThemeProvider'
import { ToastProvider } from '@src/components/toast/ToastProvider'
import { ABTestingProvider } from '@src/lib/abTester'
import { Db } from '@src/lib/db'
import intl from '@src/lib/intl'
import { queryClient } from '@src/lib/queryClient'
import { OnboardingProvider } from '@src/onboarding/OnboardingProvider'
import { QueryClientProvider } from '@tanstack/react-query'
import { createRoot } from 'react-dom/client'
import { RawIntlProvider } from 'react-intl'

import { TooltipProvider } from '@toggl/ui'

import Popup from './Popup'

function init() {
  Db.loadAll()
  const rootContainer = document.querySelector('#__root')
  if (!rootContainer) throw new Error("Can't find Popup root element")
  const root = createRoot(rootContainer)

  root.render(
    <ABTestingProvider>
      <QueryClientProvider client={queryClient}>
        <RawIntlProvider value={intl}>
          <AppThemeProvider injectCSSVarAsGlobal>
            <OnboardingProvider>
              <TooltipProvider>
                <ToastProvider position='bottom'>
                  <Popup />
                </ToastProvider>
              </TooltipProvider>
            </OnboardingProvider>
          </AppThemeProvider>
        </RawIntlProvider>
      </QueryClientProvider>
    </ABTestingProvider>
  )
}

init()
