import * as React from 'react'
import Browser from 'webextension-polyfill'

type Channel = Browser.Runtime.Port | null

export const usePomodoroMessageBus = () => {
  const [channel, setChannel] = React.useState<Channel>(null)

  React.useEffect(() => {
    const channel = Browser.runtime.connect({ name: 'POPUP' })

    setChannel(channel)

    return () => {
      channel.disconnect()
    }
  }, [])

  const start = React.useCallback(() => {
    if (channel) {
      channel.postMessage({ type: 'Pomodoro.start' })
    }
  }, [channel])

  const goToNextState = React.useCallback(() => {
    if (channel) {
      channel.postMessage({ type: 'Pomodoro.goToNextState' })
    }
  }, [channel])

  return { start, goToNextState } as const
}
