const messages = defineMessages({
  emptyList: {
    id: 'timeEntriesList.emptyList',
    defaultMessage: 'Get ready to track time and boost your productivity!',
  },
})

const NO_DESCRIPTION = '(no description)'
const NO_PROJECT = '(no project)'

// Optimize this???'
const groupDuration = (entries: TimeEntryV9[][]) => {
  const total = entries
    // Our minimum supported browser versions don't have Array.flat
    .reduce((acc, val) => acc.concat(val), [])
    .reduce((sum, { duration }) => sum + duration, 0)

  return formatDuration(DateTime.now().minus({ seconds: total }).toISO())
}

interface TimeEntriesListProps {
  projects: Record<string, Project>
  data: Record<string, TimeEntryV9[][]>
}
export default function TimeEntriesList({
  data,
  projects,
}: TimeEntriesListProps) {
  const [current] = useCurrentTimeEntryStorage()

  if (Object.keys(data).length === 0) {
    return (
      <React.Fragment>
        <div />
        {!current ? (
          <ClearerOnboarding />
        ) : (
          <NoEntries>
            <img src={emptyListImg} width='177px' height='179px' />
            <NoEntriesDescription>
              <FormattedMessage {...messages.emptyList} />
            </NoEntriesDescription>
          </NoEntries>
        )}
      </React.Fragment>
    )
  }

  return (
    <React.Fragment>
      {Object.entries(data).map(([date, group], idx) => (
        <EntryDayGroup key={idx}>
          <EntryHeading key={`tegroup-${idx}`}>
            <span>{DateTime.fromISO(date).toFormat('EEE, d MMM')}</span>
            <span>{groupDuration(group)}</span>
          </EntryHeading>

          {group.map((items, i) => (
            <TimeEntriesListItem
              key={`te-${idx}-${i}`}
              timeEntries={items}
              project={
                (items[0].project_id && projects[items[0].project_id]) || null
              }
            />
          ))}
        </EntryDayGroup>
      ))}
    </React.Fragment>
  )
}

type TimeEntriesListItemProps = {
  timeEntries: Array<TimeEntryV9>
  project: Project | null
}

function TimeEntriesListItem({
  timeEntries,
  project,
}: TimeEntriesListItemProps) {
  const { changeEditingEntry, changeView } = useExtensionContext()
  const [isGroupCollapsed, setGroupCollapsed] = React.useState(true)
  const timeEntry = timeEntries[0]

  const totalDuration = timeEntries.reduce((sum, entry) => {
    if (entry.duration > 0) sum += entry.duration
    return sum
  }, 0)
  const entriesCount = timeEntries.length
  const earliestStartTime = DateTime.fromISO(
    timeEntries[timeEntries.length - 1].start
  ).toFormat('HH:mm')

  const isGrouped = entriesCount > 1 && isGroupCollapsed
  const toggleGrouping = React.useCallback(
    () => setGroupCollapsed((val) => !val),
    [setGroupCollapsed]
  )

  const editEntry = (e: React.MouseEvent, timeEntry: TimeEntryV9) => {
    e.preventDefault()

    changeEditingEntry(timeEntry)
    changeView('editEntry')
  }

  const content = [
    entriesCount > 1 && (
      <TimeEntryRow
        renderHeader
        key={`grouped-${timeEntry.id}`}
        timeEntry={timeEntry}
        onRowClick={toggleGrouping}
        project={project}
        entriesCount={entriesCount}
        earliestStartTime={earliestStartTime}
        totalDuration={totalDuration}
      />
    ),
  ].concat(
    (!isGrouped ? timeEntries : []).map((timeEntry) => (
      <TimeEntryRow
        renderHeader={false}
        key={timeEntry.id}
        timeEntry={timeEntry}
        onRowClick={(e) => editEntry(e, timeEntry)}
        project={project}
        indent={entriesCount > 1}
      />
    ))
  )

  return <React.Fragment>{content}</React.Fragment>
}

function TimeEntryRow({
  timeEntry,
  onRowClick,
  project,
  renderHeader,
  entriesCount,
  earliestStartTime,
  totalDuration,
  indent,
}: {
  timeEntry: TimeEntryV9
  onRowClick: (event: React.MouseEvent) => void
  project: Project | null
  renderHeader: boolean
  entriesCount?: number
  earliestStartTime?: string
  totalDuration?: number
  indent?: boolean
}) {
  const toast = useToast()
  const [workspacePreferences] = useWSPreferencesStorage()
  const startTEMutation = useStartTEMutation()

  const lockedDate = workspacePreferences?.report_locked_at

  const isLocked =
    lockedDate &&
    DateTime.fromISO(timeEntry.start) < DateTime.fromISO(lockedDate)
  const description = timeEntry.description
  const isBillable = !!timeEntry.billable
  const tags =
    timeEntry.tags && timeEntry.tags.length > 0 ? timeEntry.tags.join(', ') : ''

  const continueTimeEntry = (e: React.MouseEvent) => {
    e.preventDefault()
    e.stopPropagation()
    const start = new Date()

    const entry = {
      at: start.toISOString(),
      billable: timeEntry.billable,
      description,
      duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
      start: start.toISOString(),
      tags: timeEntry.tags ?? [],
      tag_ids: timeEntry.tag_ids ?? [],
      task_id: timeEntry.task_id,
      project_id: timeEntry.project_id,
      workspace_id: timeEntry.workspace_id,
    } as unknown as TimeEntryRequest

    startTEMutation.mutate(
      {
        entry,
        integration: { type: 'popup' },
      },
      {
        onSuccess: () => {
          track('start-time-entry', { context: 'History' })
        },
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  return (
    <EntryItem onClick={!isLocked ? onRowClick : undefined} indent={indent}>
      {isLocked ? <TimeEntryLockedIndicator /> : undefined}
      {renderHeader && entriesCount && earliestStartTime && (
        <GroupedEntryIcon>
          <GroupedEntryCounter
            title={`${entriesCount} entries since ${earliestStartTime}`}
          >
            {entriesCount}
          </GroupedEntryCounter>
        </GroupedEntryIcon>
      )}
      <EntryItemText>
        <TimeEntryDescription title={description && description}>
          {description || <Placeholder>{NO_DESCRIPTION}</Placeholder>}
        </TimeEntryDescription>
        <TimeEntryProject project={project} />
      </EntryItemText>
      <EntryItemActions>
        <TimeEntryDuration duration={totalDuration || timeEntry.duration} />
        <EntryIcons>
          {isBillable && <BillableIcon />}
          {tags && (
            <div
              css={css`
                margin-left: ${styles.spacings['1x']};
              `}
            >
              <TagsIcon title={tags} />
            </div>
          )}
          <ButtonNext
            title='Continue Time Entry'
            css={
              !startTEMutation.isLoading
                ? css`
                    padding-right: 5px;
                    padding-left: 5px;
                  `
                : null
            }
            loading={startTEMutation.isLoading}
            onClick={continueTimeEntry}
            content='icon-only'
            size='small'
            variant='tertiary'
            icon={
              <div
                css={css`
                  path {
                    fill: ${styles.colors.ui_050.regular} !important;
                  }
                  &:hover {
                    path {
                      fill: ${styles.colors.ui_050.hover} !important;
                    }
                  }
                `}
              >
                <PlayIcon size={22} />
              </div>
            }
          />
        </EntryIcons>
      </EntryItemActions>
    </EntryItem>
  )
}

