import styled from '@emotion/styled'
import { useKeyboard } from '@react-aria/interactions'
import { TETooltip } from '@src/components/TETooltip'
import { useClientsListStorage } from '@src/v4/services/ClientsListService'
import { useProjectsListStorage } from '@src/v4/services/ProjectsListService'
import { useTasksListStorage } from '@src/v4/services/TasksListService'
import { useTEListStorage } from '@src/v4/services/TEListService'
import { useWorkspacesListStorage } from '@src/v4/services/WorkspacesListService'
import fuzzySort from 'fuzzysort'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import {
  type Client,
  type Project,
  type TaskV9,
  type TimeEntryV9,
  type Workspace,
} from '@toggl/track-types'

type Props = {
  show: boolean
  hide: () => void
  description: string
  onClick: (timeEntry: TimeEntryV9, e) => void
}

type ItemRefHandle = {
  focus: () => void
}

const messages = defineMessages({
  timeEntryGroup: {
    id: 'AutocompletePopdown.timeEntries',
    defaultMessage: 'Previously tracked time entries',
  },
  taskGroup: {
    id: 'AutocompletePopdown.tasks',
    defaultMessage: 'Tasks',
  },
  projectGroup: {
    id: 'AutocompletePopdown.projects',
    defaultMessage: 'Projects',
  },
})

const MOUSEOVER_TIMEOUT = 500

const isPropValuesEqual = (subject, target, propNames) =>
  propNames.every(
    (propName) =>
      `${subject[propName]}`.toLowerCase() ===
      `${target[propName]}`.toLowerCase()
  )

const getUniqueItemsByProperties = (items, propNames) =>
  items.filter(
    (item, index, array) =>
      index ===
      array.findIndex((foundItem) =>
        isPropValuesEqual(foundItem, item, propNames)
      )
  )
const PreviouslyTracked = React.forwardRef<HTMLDivElement, Props>(
  function PreviouslyTracked({ show, hide, description, onClick }, ref) {
    const [workspaces] = useWorkspacesListStorage()
    const [timeEntries] = useTEListStorage()

    const [timeEntriesToShow, setTimeEntriesToShow] = React.useState<
      TimeEntryV9[]
    >([])

    const filtering = React.useMemo(
      () => description && description !== '',
      [description]
    )

    const [projectsToShow, setProjectsToShow] = React.useState<Project[]>([])
    const [tasksToShow, setTasksToShow] = React.useState<TaskV9[]>([])
    const [activeItem, setActiveItem] = React.useState(-1)
    const inputRefs = React.useRef<ItemRefHandle[]>([])

    const [projects] = useProjectsListStorage()
    const [tasks] = useTasksListStorage()
    const [clients] = useClientsListStorage()

    React.useEffect(() => {
      if (timeEntries) {
        const withDescription = Object.values(timeEntries).filter(
          (te) => te.description && te.stop
        )
        const filteredTimeEntries =
          description && description !== ''
            ? fuzzySort
                .go(description, withDescription, {
                  key: 'description',
                  limit: 100,
                  threshold: -10000,
                })
                .map((result) => result.obj)
            : withDescription
        const uniqTimeEntries = getUniqueItemsByProperties(
          filteredTimeEntries,
          ['description']
        )
        const timeEntriesWithoutArchivedProjects = uniqTimeEntries.filter(
          (te) => ({
            ...te,
            project_id: projects?.[`${te.project_id}`]?.active
              ? te.project_id
              : null,
          })
        )

        setTimeEntriesToShow(
          (filtering
            ? timeEntriesWithoutArchivedProjects
            : timeEntriesWithoutArchivedProjects.reverse()
          ).slice(0, 10)
        )
      }

      if (projects && filtering) {
        const projectsWithClients = Object.values(projects)
          .filter((project) => project.active)
          .map((project) => ({
            ...project,
            _filter: `${project.name} ${clients?.[`${project.cid}`]?.name}`,
          }))
        const filteredProjects =
          description && description !== ''
            ? fuzzySort
                .go(description, projectsWithClients, {
                  key: '_filter',
                  limit: 100,
                  threshold: -10000,
                })
                .map((result) => result.obj)
            : projectsWithClients
        const uniqProjects = getUniqueItemsByProperties(filteredProjects, [
          'name',
        ])
        setProjectsToShow(uniqProjects.slice(0, 10))
      }

      if (tasks && filtering) {
        const activeProjectsTasks = Object.values(tasks).filter(
          (task) => projects?.[`${task.project_id}`]?.active
        )
        const filteredTasks =
          description && description !== ''
            ? fuzzySort
                .go(description, activeProjectsTasks, {
                  key: 'name',
                  limit: 100,
                  threshold: -10000,
                })
                .map((result) => result.obj)
            : activeProjectsTasks
        const uniqTasks = getUniqueItemsByProperties(filteredTasks, [
          'name',
          'project_id',
        ])
        setTasksToShow(uniqTasks.slice(0, 10))
      }
    }, [clients, description, filtering, projects, tasks, timeEntries])

    const handleTimeEntryClick = React.useCallback(
      (timeEntry: TimeEntryV9, e) => {
        onClick(
          {
            description: timeEntry.description || '',
            workspace_id: timeEntry.workspace_id,
            project_id: timeEntry.project_id,
            task_id: timeEntry.task_id,
            tag_ids: timeEntry.tag_ids,
            billable: timeEntry.billable,
          } as TimeEntryV9,
          e
        )
        hide()
      },
      [onClick, hide]
    )

    const handleProjectClick = React.useCallback(
      (project: Project, e) => {
        onClick(
          {
            description: '',
            workspace_id: project.workspace_id,
            project_id: project.id,
            task_id: null,
            billable: project.billable,
          } as TimeEntryV9,
          e
        )
        hide()
      },
      [onClick, hide]
    )

    const handleTaskClick = React.useCallback(
      (task: TaskV9, e) => {
        const taskProject = projects?.[`${task.project_id}`]
        onClick(
          {
            description: '',
            workspace_id: task.project_id,
            project_id: task.project_id,
            task_id: task.id,
            billable: taskProject ? taskProject.billable : false,
          } as TimeEntryV9,
          e
        )
        hide()
      },
      [projects, onClick, hide]
    )

    const { keyboardProps } = useKeyboard({
      onKeyDown: (e) => {
        e.preventDefault()
        if (e.key === 'ArrowDown') {
          setActiveItem((active) =>
            active + 1 <
            timeEntriesToShow.length +
              projectsToShow.length +
              tasksToShow.length
              ? active + 1
              : 0
          )
        }
        if (e.key === 'ArrowUp' && activeItem - 1 >= 0) {
          setActiveItem((active) => active - 1)
        }
        if (e.key === 'Escape') hide()
      },
    })

    React.useEffect(() => inputRefs.current[activeItem]?.focus(), [activeItem])

    if (
      !show ||
      (!timeEntriesToShow?.length &&
        !projectsToShow?.length &&
        !tasksToShow?.length)
    )
      return null

    return (
      <React.Fragment>
        <Overlay onClick={() => hide()} />
        <Content
          tabIndex={-1}
          ref={ref}
          {...keyboardProps}
          onFocus={() => {
            if (activeItem === -1) setActiveItem(0)
          }}
        >
          <ItemsContainer>
            {!!timeEntriesToShow?.length && (
              <React.Fragment>
                <Title>
                  <FormattedMessage {...messages.timeEntryGroup} />
                </Title>
                {timeEntriesToShow.map((te, index) => (
                  <AutocompleteItem
                    key={`te-${te.id}`}
                    description={te.description}
                    project={projects?.[`${te.project_id}`]}
                    workspace={workspaces?.[`${te.workspace_id}`]}
                    onClick={(e) => handleTimeEntryClick(te, e)}
                    ref={(el) => el && (inputRefs.current[index] = el)}
                    client={
                      clients?.[`${projects?.[`${te.project_id}`]?.client_id}`]
                    }
                  />
                ))}
              </React.Fragment>
            )}
            {!!tasksToShow?.length && filtering && (
              <React.Fragment>
                <Title>
                  <FormattedMessage {...messages.taskGroup} />
                </Title>
                {tasksToShow.map((task, index) => (
                  <AutocompleteItem
                    key={`task-${task.id}`}
                    description={task.name}
                    project={projects?.[`${task.project_id}`]}
                    workspace={workspaces?.[`${task.workspace_id}`]}
                    client={
                      clients?.[
                        `${projects?.[`${task.project_id}`]?.client_id}`
                      ]
                    }
                    ref={(el) =>
                      el &&
                      (inputRefs.current[index + timeEntriesToShow.length] = el)
                    }
                    onClick={(e) => handleTaskClick(task, e)}
                  />
                ))}
              </React.Fragment>
            )}
            {!!projectsToShow?.length && filtering && (
              <React.Fragment>
                <Title>
                  <FormattedMessage {...messages.projectGroup} />
                </Title>
                {projectsToShow.map((project, index) => (
                  <AutocompleteItem
                    key={`project-${project.id}`}
                    project={project}
                    workspace={workspaces?.[`${project.workspace_id}`]}
                    client={clients?.[`${project.client_id}`]}
                    showClient
                    ref={(el) =>
                      el &&
                      (inputRefs.current[
                        index + timeEntriesToShow.length + tasksToShow.length
                      ] = el)
                    }
                    onClick={(e) => handleProjectClick(project, e)}
                  />
                ))}
              </React.Fragment>
            )}
          </ItemsContainer>
        </Content>
      </React.Fragment>
    )
  }
)

export default PreviouslyTracked

type AutocompleteItemProps = {
  description?: string
  project?: Project
  client?: Client
  showClient?: boolean
  workspace?: Workspace
  onClick: (e: React.MouseEvent<HTMLLIElement>) => void
}

const AutocompleteItem = React.forwardRef<ItemRefHandle, AutocompleteItemProps>(
  function AutocompleteItem(
    { description, project, client, showClient = false, workspace, onClick },
    ref
  ) {
    const [isHovered, setIsHovered] = React.useState(false)
    const elementRef = React.useRef<HTMLLIElement>(null)

    let closureTimeout: NodeJS.Timeout | undefined

    const handleMouseOver = () => {
      if (closureTimeout) {
        clearTimeout(closureTimeout)
      }
      closureTimeout = setTimeout(() => setIsHovered(true), MOUSEOVER_TIMEOUT)
    }

    const handleMouseLeave = () => {
      if (closureTimeout) {
        clearTimeout(closureTimeout)
      }
      closureTimeout = setTimeout(
        () => setIsHovered(false),
        MOUSEOVER_TIMEOUT / 2
      )
    }

    React.useImperativeHandle(
      ref,
      () => ({
        focus() {
          elementRef.current?.focus()
        },
      }),
      []
    )

    const { keyboardProps } = useKeyboard({
      onKeyDown(e) {
        if (e.key === 'Enter') return onClick(e as any)
        e.continuePropagation()
      },
    })

    return (
      <React.Fragment>
        <Item
          {...keyboardProps}
          onClick={onClick}
          ref={elementRef}
          onMouseOver={handleMouseOver}
          onMouseLeave={handleMouseLeave}
          tabIndex={-1}
        >
          {description && <DescriptionLabel>{description}</DescriptionLabel>}
          {project && (
            <ProjectLabel color={project.color}>{project.name}</ProjectLabel>
          )}
          {client && showClient && <ClientLabel>{client.name}</ClientLabel>}
        </Item>
        {isHovered && (
          <TETooltip
            description={description}
            project={project}
            client={client}
            workspace={workspace}
            positionRef={elementRef}
          />
        )}
      </React.Fragment>
    )
  }
)

const Overlay = styled.div`
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;

  height: 100vh;
  width: 100vw;
  background: transparent;
`

const Content = styled.div`
  z-index: 2;
  position: absolute;
  top: 100px;
  left: 8px;
  right: 8px;

  border-radius: 8px;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(0, 0, 0, 0.1);

  flex-flow: column nowrap;
  max-height: 350px;
  height: fit-content;
  padding: 2px 8px;

  overflow: auto;
  background: ${({ theme }) => theme.colors.surface_primary.regular};
  color: ${({ theme }) => theme.colors.ui.regular};
  display: 'block';
`

const ItemsContainer = styled.ul`
  padding: 0px;
  list-style: none;
  margin: 0;
  gap: 2px;
`

const Item = styled.li`
  height: 25px;
  line-height: 25px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  gap: 1px;

  padding: 2px 8px;
  margin: 2px 0;

  font-size: 14px;
  color: ${({ theme }) => theme.colors.ui.regular};
  text-overflow: ellipsis;
  overflow: hidden;
  word-wrap: break-word;

  &:hover {
    color: ${({ theme }) => theme.colors.ui.hover};
    background-color: ${({ theme }) => theme.colors.surface_primary.hover};
    cursor: pointer;
  }

  &:focus {
    color: ${({ theme }) => theme.colors.ui.hover};
    background-color: ${({ theme }) => theme.colors.surface_primary.hover};
    cursor: pointer;
    outline: none;
  }
`

const Title = styled.li`
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 8px;

  font-style: normal;
  font-weight: ${({ theme }) => theme.typography.fontWeight.semibold};
  font-size: 11px;
  color: ${({ theme }) => theme.colors.ui_060.regular};

  cursor: default;
  text-transform: uppercase;
  letter-spacing: 0.4px;
`

const DescriptionLabel = styled.span`
  text-overflow: ellipsis;
  overflow: hidden;

  margin-right: 25px;
  white-space: nowrap;
  font-size: 14px;
  color: ${({ theme }) => theme.colors.ui.regular};
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
`

const ProjectLabel = styled.span`
  text-overflow: ellipsis;
  overflow: hidden;

  white-space: nowrap;
  font-size: 14px;
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  color: ${(props) => props.color};

  &:before {
    display: inline-block;
    margin: 0 8px 0px 4px;
    line-height: 1;

    content: '';
    font-family: Arial, serif;
    font-size: 30px;
    font-weight: bold;
    color: ${(props) => props.color};
    vertical-align: middle;
  }
`

const ClientLabel = styled.span`
  font-size: 14px;
  color: ${({ theme }) => theme.colors.ui_060.regular};
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  white-space: 'nowrap';

  text-overflow: ellipsis;
  overflow: hidden;
  padding-left: 15px;

  &:before {
    display: inline-block;
    margin: 0px 8px 0px 4px;
    line-height: 1;

    content: '';
    font-family: Arial, serif;
    font-size: 16px;
    font-weight: bold;
    color: ${({ theme }) => theme.colors.ui_060.regular};
    vertical-align: middle;
  }
`
