import { css } from '@emotion/react'
import styled from '@emotion/styled'
import { AppThemeProvider } from '@src/components/AppThemeProvider'
import { useToast } from '@src/components/toast/ToastProvider'
import { WorkspacePopdown } from '@src/components/WorkspacePopdown'
import { useFullFetchStatus } from '@src/lib/full-fetch'
import { useMutation } from '@tanstack/react-query'
import Logo from '@ui/icons/toggl-button'
import { FormattedMessage, defineMessages } from 'react-intl'
import * as Browser from 'webextension-polyfill'

import styles from '@toggl/style'
import {
  ButtonNext,
  ExternalLinkIconNext,
  ReconnectIconNext,
  SettingsIconNext,
  Tooltip,
  WarningIconNext,
} from '@toggl/ui'

const messages = defineMessages({
  openSettings: {
    id: 'header.links.openSettings',
    defaultMessage: 'Open Settings',
  },
  sync: {
    id: 'header.links.sync',
    defaultMessage: 'Sync',
  },
  goToWebapp: {
    id: 'header.links.goToWebapp',
    defaultMessage: 'Open Toggl',
  },
})

export default function Header() {
  const toast = useToast()
  const [fullFetch] = useFullFetchStatus()

  const onFullFetch = async () => {
    await Browser.runtime.sendMessage({
      type: 'activate',
      payload: { forwardError: true },
    })
  }

  const fullFetchMutation = useMutation(onFullFetch)

  const handleFetchClick = () =>
    fullFetchMutation.mutate(undefined, {
      onError(error: Error) {
        toast.addToast({
          type: 'error',
          message: error.message,
          title: 'Sync error',
        })
      },
    })

  return (
    <AppThemeProvider enforceColorScheme='dark'>
      <Root>
        <Logo active />
        <WorkspacePopdown />
        <IconsWrapper>
          <FormattedMessage {...messages.goToWebapp}>
            {(message) => (
              <ButtonNext
                onClick={() =>
                  Browser.tabs.create({
                    url: `https://${process.env.TRACK_HOSTNAME}?utm_source=toggl-button&utm_medium=referral`,
                  })
                }
                title={String(message)}
                content='icon-only'
                size='small'
                icon={<ExternalLinkIconNext />}
                variant='tertiary'
              />
            )}
          </FormattedMessage>

          <FormattedMessage {...messages.sync}>
            {(message) => (
              <Tooltip enabled={!!fullFetch?.error}>
                <ButtonNext
                  loading={fullFetchMutation.isLoading || fullFetch?.loading}
                  onClick={handleFetchClick}
                  title={String(message)}
                  content='icon-only'
                  size='small'
                  icon={
                    <ReconnectIconNext
                      color={fullFetch?.error ? 'error' : 'ui'}
                    />
                  }
                  variant='tertiary'
                />
                <Tooltip.Content>
                  {fullFetch?.error && (
                    <div
                      css={css`
                        ${styles.typography.p1Body}
                        display: flex;
                        align-items: center;
                        gap: ${styles.spacings['1x']};
                      `}
                    >
                      <WarningIconNext color='error' />
                      {fullFetch?.error}
                    </div>
                  )}
                </Tooltip.Content>
              </Tooltip>
            )}
          </FormattedMessage>

          <FormattedMessage {...messages.openSettings}>
            {(message) => (
              <ButtonNext
                onClick={() =>
                  Browser.tabs.create({
                    url: Browser.runtime.getURL('settings.html'),
                  })
                }
                title={String(message)}
                content='icon-only'
                size='small'
                icon={<SettingsIconNext />}
                variant='tertiary'
              />
            )}
          </FormattedMessage>
        </IconsWrapper>
      </Root>
    </AppThemeProvider>
  )
}

const Root = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 48px;
  padding: 0 8px;
  background-color: #2c1338;
  z-index: 10;
  gap: 5px;
`

const IconsWrapper = styled.div`
  display: flex;
  flex-direction: row;
  gap: 4px;
  align-items: center;
  margin-left: auto;
`
