import * as RdxTooltip from '@radix-ui/react-tooltip'
import * as React from 'react'

/**
 * Wrap your application root with TooltipProvider to enable Tooltip
 * functionality. See:
 * https://www.radix-ui.com/docs/primitives/components/tooltip#provider.
 */
export const TooltipProvider = ({
  children,
  delayDuration = 500,
  ...rest
}: React.ComponentProps<typeof RdxTooltip.Provider>) => (
  <RdxTooltip.Provider delayDuration={delayDuration} {...rest}>
    {children}
  </RdxTooltip.Provider>
)
