import { useTheme } from '@emotion/react'
import styled from '@emotion/styled'
import { Tooltip } from '@src/ui/components/Tooltip'
import type * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import { type Client, type Project, type Workspace } from '@toggl/track-types'

type Props = {
  className?: string
  positionRef: React.RefObject<HTMLLIElement>
  description?: string
  project?: Project
  client?: Client
  workspace?: Workspace
}

const messages = defineMessages({
  noProject: {
    id: 'TETooltip.noProject',
    defaultMessage: 'No project',
  },
})

export function TETooltip({
  className,
  description,
  positionRef,
  project,
  client,
  workspace,
}: Props) {
  const theme = useTheme()

  return (
    <Root
      className={className}
      elementRef={positionRef.current!}
      placement='auto'
      arrow
    >
      <TooltipContent>
        {description && <DescriptionLabel>{description}</DescriptionLabel>}
        {project ? (
          <ProjectLabel color={project.color}>{project.name}</ProjectLabel>
        ) : (
          <ProjectLabel color={theme.colors.ui_060.regular}>
            <FormattedMessage {...messages.noProject} />
          </ProjectLabel>
        )}
        {client && <ClientLabel>{client.name}</ClientLabel>}
        <Divider />
        <WorkspaceLabel>{workspace && workspace.name}</WorkspaceLabel>
      </TooltipContent>
    </Root>
  )
}

const Root = styled(Tooltip)`
  min-width: fit-content;
  max-width: 500px;
`

const DescriptionLabel = styled.span`
  margin-right: 25px;
  white-space: 'normal';
  font-size: '12px';
  color: ${({ theme }) => theme.colors.ui.regular};
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
`

const ProjectLabel = styled.span`
  white-space: 'normal';
  font-size: '12px';
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  color: ${(props) => props.color};

  &:before {
    display: inline-block;
    margin: 0 8px 0px 4px;
    line-height: 1;

    content: '';
    font-family: Arial, serif;
    font-size: 30px;
    font-weight: bold;
    color: ${(props) => props.color};
    vertical-align: middle;
  }
`

const ClientLabel = styled.span`
  font-size: '12px';
  color: ${({ theme }) => theme.colors.ui_060.regular};
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
  white-space: 'normal';
`

const WorkspaceLabel = styled.span`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  font-size: 12px;
  color: ${({ theme }) => theme.colors.ui_060.regular};
  font-weight: ${({ theme }) => theme.typography.fontWeight.normal};
`

const Divider = styled.div`
  width: 38px;
  margin: 10px 0;
  border-top: 1px solid ${({ theme }) => theme.colors.ui_020.regular};
`

const TooltipContent = styled.div`
  display: flex;
  flex-direction: column;
  flex-grow: 0;
`
