import { useToast } from '@src/components/toast/ToastProvider'
import { useExtensionContext } from '@src/context'
import { track } from '@src/lib/utils'
import { usePomodoroSettings } from '@src/pomodoro/usePomodoroSettings'
import { usePopupContext } from '@src/shared/providers/PopupProvider'
import { useCurrentTimeEntryStorage } from '@src/v4/services/CurrentTEService'
import { useSelectedWorkspaceIdStorage } from '@src/v4/services/SelectedWorkspaceService'
import { useStartTEMutation } from '@src/v4/services/StartTEService'
import { DateTime } from 'luxon'
import * as React from 'react'

import { type TimeEntryRequest } from '@toggl/track-types'
import { ButtonNext } from '@toggl/ui'

import { AutoTimerForm } from './AutoTimerForm'
import { RunningTimer } from './RunningTimer'
import { HeaderContainer, TimerContainer } from './styles'
import TimeEntryDescription from './TimeEntryDescription'
import { TimerDropdowns } from './TimerDropdowns'
import { type EditingEntry } from './types'

export type TimerMode = 'manual' | 'auto'

export function Timer({
  hideSwitcher,
  disableAutocomplete,
  heapContext,
  ...props
}: {
  hideSwitcher?: boolean
  disableAutocomplete?: boolean
  heapContext?: string
}) {
  const [current] = useCurrentTimeEntryStorage()

  return current ? (
    <RunningTimer
      current={current}
      disableAutocomplete={disableAutocomplete}
      {...props}
    />
  ) : (
    <TimerForm
      hideSwitcher={hideSwitcher}
      disableAutocomplete={disableAutocomplete}
      heapContext={heapContext}
      {...props}
    />
  )
}

function TimerForm({
  hideSwitcher,
  disableAutocomplete,
  heapContext,
  ...props
}: {
  hideSwitcher?: boolean
  disableAutocomplete?: boolean
  heapContext?: string
}) {
  const toast = useToast()
  const { user } = useExtensionContext()
  const [_timeEntry, setTimeEntry] = React.useState<EditingEntry>({})
  const [description, setDescription] = React.useState('')
  const startTEMutation = useStartTEMutation()
  const [selectedWSId] = useSelectedWorkspaceIdStorage()
  const { setActiveView } = usePopupContext()
  const [settings] = usePomodoroSettings()

  const startTimer = React.useCallback(
    (e, te?) => {
      e.preventDefault()
      if (!user) return
      const start = _timeEntry.start ? new Date(_timeEntry.start) : new Date()

      const _entry = te ?? _timeEntry
      const entry = {
        at: start.toISOString(),
        billable: false,
        description,
        duration: -parseInt((start.getTime() / 1000).toFixed(0), 10),
        start: start.toISOString(),
        tags: [],
        tag_ids: [],
        workspace_id: selectedWSId as number,
        ..._entry,
      } as unknown as TimeEntryRequest

      startTEMutation.mutate(
        {
          entry,
          integration: { type: 'popup' },
        },
        {
          onError: (error) => {
            toast.addToast({
              title: 'Something went wrong',
              message: error.message,
              type: 'error',
            })
          },
        }
      )
    },
    [_timeEntry, description, selectedWSId, startTEMutation, toast, user]
  )

  const createEntry = (e) => {
    e.preventDefault()
    if (!user) return
    const duration = Math.floor(
      (DateTime.fromISO(_timeEntry.stop!).toMillis() -
        DateTime.fromISO(_timeEntry.start!).toMillis()) /
        1000
    )

    const entry = {
      at: DateTime.now().toISO(),
      billable: false,
      duration,
      tags: [],
      tag_ids: [],
      workspace_id: selectedWSId as number,
      ..._timeEntry,
      description,
    } as unknown as TimeEntryRequest

    return startTEMutation.mutateAsync(
      {
        entry,
        integration: { type: 'popup' },
      },
      {
        onSuccess: (entry) => {
          // TODO: Check this.
          setTimeEntry({
            ...entry,
            start: entry.stop!,
            stop: DateTime.fromISO(entry.stop!)
              .plus({ seconds: entry?.duration })
              .toISO(),
          })
        },
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  const onDatesChange = React.useCallback(
    (start, stop) => {
      setTimeEntry((entry) => ({ ...entry, start, stop }))
    },
    [setTimeEntry]
  )

  const handleDropdownsChange = React.useCallback(
    (changes: {
      billable: boolean
      selectedTask?: number
      selectedProject?: number | null
      selectedTags?: number[] | null
    }) => {
      setTimeEntry((entry) => ({
        ...entry,
        billable: changes.billable,
        tag_ids: changes.selectedTags,
        project_id: changes.selectedProject,
        task_id: changes.selectedTask,
      }))
    },
    []
  )

  const handleAutocompleteChange = React.useCallback(
    (entry, e) => {
      setTimeEntry({
        project_id: entry.project_id,
        task_id: entry.task_id,
        tag_ids: entry.tag_ids,
        billable: entry.billable,
      })
      setDescription(entry.description)

      startTimer(e, {
        project_id: entry.project_id,
        task_id: entry.task_id,
        tag_ids: entry.tag_ids,
        billable: entry.billable,
        description: entry.description,
      })
      track('start-time-entry', { context: 'Autocomplete' })
    },
    [startTimer]
  )

  return (
    <HeaderContainer {...props}>
      <TimerContainer>
        {settings?.isEnabled && (
          <ButtonNext
            variant='tertiary'
            type='button'
            size='regular'
            content='text'
            css={{
              fontSize: 24,
              padding: 0,
              aspectRatio: 1,
              borderRadius: '50%',
              marginRight: 8,
            }}
            onClick={() =>
              setActiveView((current) =>
                current === 'LIST' ? 'POMODORO' : 'LIST'
              )
            }
          >
            <img
              src='/images/pomodoro-icon.png'
              alt='Pomodoro Icon'
              height={28}
            />
          </ButtonNext>
        )}

        <TimeEntryDescription
          description={description}
          onChange={(value) => setDescription(value)}
          startTimer={startTimer}
          onSelectTimeEntry={handleAutocompleteChange}
          disableAutocomplete={disableAutocomplete}
        />
        <AutoTimerForm
          loading={startTEMutation.isLoading}
          setTimeEntry={setTimeEntry}
          timeEntry={_timeEntry}
          startTimer={startTimer}
          createEntry={createEntry}
          onDatesChange={onDatesChange}
          heapContext={heapContext}
        />
      </TimerContainer>

      <TimerDropdowns
        onDropdownsChange={handleDropdownsChange}
        timeEntry={_timeEntry}
      />
    </HeaderContainer>
  )
}
