import _ from 'lodash'

import { type TimeEntryV9 } from '@toggl/track-types'

const createGroupKey = (data: TimeEntryV9): string => {
  const date = data.start.split('T')[0] // Faster than using DateTime
  const tags = data.tags ? data.tags.slice().sort().join(',') : ''

  return [
    date,
    data.description,
    data.project_id,
    data.task_id,
    data.workspace_id,
    data.billable ? '1' : '0',
    tags,
  ].join('|')
}

export const getGroupedTEs = (items: TimeEntryV9[]) => {
  const sortedItems = _.chain(items)
    .filter((item) => item.duration >= 0)
    .sortBy('start')
    .reverse()
    .value()

  // Group by date and then by createGroupKey
  const groupedAndSorted = _.chain(sortedItems)
    .groupBy((item) => item.start.split('T')[0])
    .mapValues((itemsGroupedByDate) => {
      return _.chain(itemsGroupedByDate)
        .groupBy(createGroupKey)
        .values()
        .value()
    })
    .value()

  return groupedAndSorted
}
