import { logger } from '@src/lib/errorReport'
import { type MessageFrom } from '@src/typdef'
import { useMutation } from '@tanstack/react-query'
import Browser from 'webextension-polyfill'

import { type TimeEntryV9 } from '@toggl/track-types'

type DispatchContext = { entry: TimeEntryV9; from: MessageFrom }
type MutationContext = { entry: TimeEntryV9; integration: MessageFrom }

const UpdateTEService = {
  // WIP: Still using legacy event 'update-entry' to keep logic.
  dispatch(data: DispatchContext): Promise<TimeEntryV9> {
    return Browser.runtime.sendMessage({
      type: 'update-entry',
      payload: data,
    })
  },
}

type MutationData = TimeEntryV9

const useUpdateTEMutation = () => {
  return useMutation<MutationData, Error, MutationContext>(
    (data) =>
      UpdateTEService.dispatch({ entry: data.entry, from: data.integration }),
    {
      onError: (error) => {
        return logger.error(error)
      },
    }
  )
}

export { UpdateTEService, useUpdateTEMutation }
