import { type PomodoroState } from '@src/pomodoro/PomodoroService'

export const getActionLabel = (state: PomodoroState) => {
  if (!state.isRunning && !state.isBreak && !state.isLargeBreak) {
    return 'Start Session'
  }

  if (!state.isRunning && (state.isBreak || state.isLargeBreak)) {
    return 'Start Break'
  }

  if (state.isRunning && (state.isBreak || state.isLargeBreak)) {
    return 'Skip Break'
  }

  return 'Stop'
}
