import { css } from '@emotion/react'
import styled from '@emotion/styled'
import { BillableTrigger } from '@src/components/BillableTrigger'
import { DateTimeInput } from '@src/components/dateTimeInput/DateTimeInput'
import Dropdown from '@src/components/Dropdown'
import { ProjectsDropdown } from '@src/components/ProjectsDropdown'
import { TagsDropdown } from '@src/components/TagsDropdown'
import { DropdownsContainer } from '@src/components/timer/styles'
import { useToast } from '@src/components/toast/ToastProvider'
import { UnmetConstraintsTooltip } from '@src/components/UnmetConstraintsTooltip'
import { useExtensionContext } from '@src/context'
import {
  calculateStopDate,
  formatDuration,
  parseDuration,
} from '@src/lib/timerUtils'
import { DurationInput, FieldWrapper, TextInput } from '@src/ui/components'
import { useCurrentTimeEntryStorage } from '@src/v4/services/CurrentTEService'
import { useDeleteTEMutation } from '@src/v4/services/DeleteTEService'
import { useProjectsListStorage } from '@src/v4/services/ProjectsListService'
import { useSelectedWorkspaceIdStorage } from '@src/v4/services/SelectedWorkspaceService'
import { useTagsListStorage } from '@src/v4/services/TagsListService'
import { TEConstraintsService } from '@src/v4/services/TEConstraintsService'
import { useTEListStorage } from '@src/v4/services/TEListService'
import { useUpdateTEMutation } from '@src/v4/services/UpdateTEService'
import { useWorkspace } from '@src/v4/services/WorkspacesListService'
import moment from 'moment'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'
import { type MessageFrom } from 'src/typdef'

import styles from '@toggl/style'
import { ButtonNext } from '@toggl/ui'

const messages = defineMessages({
  doneButton: {
    id: 'editTimeEntry.doneButton',
    defaultMessage: 'Done',
  },
  cancelButton: {
    id: 'editTimeEntry.cancelButton',
    defaultMessage: 'Cancel',
  },
  deleteButton: {
    id: 'editTimeEntry.cancelButton',
    defaultMessage: 'Delete',
  },
  billableText: {
    id: 'editTimeEntry.billableText',
    defaultMessage: 'Billable',
  },
  duration: {
    id: 'editTimeEntry.duration',
    defaultMessage: 'Duration',
  },
})

type ContentPosition = { left: number; top: number; width?: number }

export const EditTimeEntry: React.FC<{
  submitOrCancelCB?: () => void
  fromIntegration: MessageFrom
  hideDurationInput?: boolean
  projectPopdownPosition?: ContentPosition
  tagPopdownPosition?: ContentPosition
  durationPopdownPosition?: ContentPosition
}> = (props) => {
  const toast = useToast()
  const [, setCurrent] = useCurrentTimeEntryStorage()
  const [, setTimeEntries] = useTEListStorage()
  const [selectedWSId] = useSelectedWorkspaceIdStorage()

  const { editingEntry, changeEditingEntry, changeView } = useExtensionContext()

  const updateTEMutation = useUpdateTEMutation()
  const deleteTEMutation = useDeleteTEMutation()

  // Use the missing constraints to show the tooltip.
  const ws = useWorkspace(editingEntry?.workspace_id)

  const [projects] = useProjectsListStorage()
  const [tags] = useTagsListStorage()

  const [submiting, setSubmiting] = React.useState(false)
  const [deleting, setDeleting] = React.useState(false)
  const doneRef = React.useRef(null)

  const [billable, setBillable] = React.useState(editingEntry?.billable)

  const [selectedTagsIds, setSelectedTagsIds] = React.useState(
    (editingEntry?.tag_ids ?? []) as number[]
  )

  const [selectedProjectId, setSelectedProjectId] = React.useState(
    editingEntry?.project_id
  )
  const [selectedTaskId, setSelectedTaskId] = React.useState(
    editingEntry?.task_id
  )

  const [duration, setDuration] = React.useState(
    formatDuration(editingEntry?.start as string, editingEntry?.stop as string)
  )

  const [description, setDescription] = React.useState(
    editingEntry?.description ?? ''
  )

  const updatedTE = React.useMemo(
    () => ({
      ...editingEntry,
      description,
      billable: !!billable,
      tag_ids: selectedTagsIds,
      project_id: selectedProjectId,
      task_id: selectedTaskId,
      start: editingEntry?.start,
      stop: editingEntry?.stop,
      duration: editingEntry?.duration,
    }),
    [
      editingEntry,
      selectedTagsIds,
      selectedProjectId,
      billable,
      selectedTaskId,
      description,
    ]
  )

  const missingProps = TEConstraintsService.check(
    ws?.te_constraints ?? null,
    updatedTE
  )

  const wid =
    projects && selectedProjectId
      ? projects[selectedProjectId]?.workspace_id
      : selectedWSId

  const updateDuration = (e: React.FocusEvent<HTMLInputElement, Element>) => {
    if (!editingEntry || editingEntry.duration < 0) return

    changeEditingEntry({
      ...editingEntry,
      stop: calculateStopDate(
        editingEntry.start,
        parseDuration(e.target.value).toMillis() / 1000
      ),
      duration: parseDuration(e.target.value).toMillis() / 1000,
    })
  }

  const updateEditingTETimeFields = (start: string, stop: string) => {
    if (!editingEntry || editingEntry.duration < 0) return

    const startValue = moment(start)
    const stopValue = moment(stop)

    const duration = stopValue.diff(startValue, 'seconds')

    changeEditingEntry({
      ...editingEntry,
      start: startValue.toISOString(),
      stop: stopValue.toISOString(),
      duration,
    })
  }

  React.useEffect(() => {
    setDuration(
      formatDuration(
        editingEntry?.start as string,
        editingEntry?.stop as string
      )
    )
  }, [editingEntry])

  const handleTagsChange = (id: number) => {
    selectedTagsIds?.includes(id)
      ? setSelectedTagsIds(selectedTagsIds.filter((tagId) => tagId !== id))
      : setSelectedTagsIds([id, ...selectedTagsIds])
  }

  const handleSubmit = (e: React.MouseEvent) => {
    e.preventDefault()
    if (!editingEntry) return
    setSubmiting(true)

    updateTEMutation.mutate(
      {
        entry: {
          ...editingEntry,
          description,
          billable: !!billable,
          tag_ids: selectedTagsIds,
          project_id: selectedProjectId,
          task_id: selectedTaskId,
          start: editingEntry.start,
          stop: editingEntry.stop,
          duration: editingEntry.duration,
        },
        integration: props.fromIntegration,
      },
      {
        onSuccess: (entry) => {
          setTimeEntries((prev) => ({
            ...prev,
            [entry.id]: entry,
          }))

          changeView('default')
          changeEditingEntry(undefined)
          if (props.submitOrCancelCB) props.submitOrCancelCB()
        },
        onSettled: () => {
          setSubmiting(false)
        },
        onError: (error) => {
          toast.addToast({
            title: 'Something went wrong',
            message: error.message,
            type: 'error',
          })
        },
      }
    )
  }

  const handleDelete = (e: React.MouseEvent) => {
    e.preventDefault()
    if (!editingEntry) return
    setDeleting(true)

    deleteTEMutation.mutate(editingEntry, {
      onSuccess: (_, vars) => {
        setCurrent(null)
        setTimeEntries((prev) => {
          const newEntries = { ...prev }
          delete newEntries[vars.id]
          return newEntries
        })

        changeView('default')
        changeEditingEntry(undefined)
        if (props.submitOrCancelCB) props.submitOrCancelCB()
      },
      onSettled: () => {
        setDeleting(false)
      },
      onError: (error) => {
        toast.addToast({
          title: 'Something went wrong',
          message: error.message,
          type: 'error',
        })
      },
    })
  }

  return (
    <Root id='homeRoot'>
      <FormWrapper>
        {props.fromIntegration.type === 'popup' && !props.hideDurationInput && (
          <FieldWrapper
            css={css`
              margin-top: ${styles.spacings['1.5x']};
              margin-bottom: ${styles.spacings['2x']};
            `}
          >
            <Label>
              <FormattedMessage {...messages.duration} />
            </Label>
            <Dropdown onItemClick={(e) => null}>
              <Dropdown.RawTrigger>
                <DurationInput
                  css={css`
                    ${styles.typography.p1Body};
                    text-align: left;
                  `}
                  value={duration}
                  onChange={(e) => setDuration(e.target.value)}
                  onBlur={updateDuration}
                />
              </Dropdown.RawTrigger>
              <Dropdown.Overlay />
              <Dropdown.DateTimeContent
                position={props.durationPopdownPosition}
              >
                <DateTimeInput
                  disableManualCreation
                  start={editingEntry?.start}
                  stop={
                    editingEntry?.stop &&
                    moment(editingEntry.stop).format('HH:mm')
                  }
                  onDatesChange={updateEditingTETimeFields}
                />
              </Dropdown.DateTimeContent>
            </Dropdown>
          </FieldWrapper>
        )}
        <FieldWrapper>
          <TextInput
            css={css`
              width: 100% !important;
              border-color: ${styles.colors.ui_060.regular};
            `}
            type='text'
            placeholder='What are you doing?'
            value={description}
            onKeyDown={(e) => {
              e.stopPropagation()
            }}
            onChange={(e) => setDescription(e.target.value)}
          />
        </FieldWrapper>
        <DropdownsContainer
          css={css`
            position: relative;
            flex-wrap: wrap;
            padding: 0;
          `}
        >
          <div
            css={css`
              overflow: hidden;
              text-overflow: ellipsis;
              max-width: 285px;
            `}
          >
            <ProjectsDropdown
              contentPosition={
                props.projectPopdownPosition ?? { left: 0, top: 40 }
              }
              selectedProjectId={selectedProjectId}
              selectedTaskId={selectedTaskId}
              onChange={(changes) => {
                setSelectedProjectId(changes.projectId)
                setSelectedTaskId(changes.taskId)
                setBillable(changes.billable)
              }}
              maxHeight={276}
            />
          </div>

          <TagsDropdown
            contentPosition={props.tagPopdownPosition ?? { left: 0, top: 40 }}
            wid={wid}
            tagsMap={tags}
            selectedIds={selectedTagsIds}
            onChange={handleTagsChange}
            maxHeight={238}
          />

          <BillableTrigger billable={billable} onChange={setBillable} />
        </DropdownsContainer>
        <UnmetConstraintsTooltip
          enabled={!!missingProps.length}
          placement='bottom'
          missingProps={missingProps}
        >
          <FieldWrapper ref={doneRef}>
            <ButtonNext
              loading={submiting}
              disabled={missingProps.length > 0}
              onClick={handleSubmit}
              content='text'
              size='regular'
              variant='primary'
              css={css`
                width: 100%;
              `}
            >
              <FormattedMessage {...messages.doneButton} />
            </ButtonNext>
          </FieldWrapper>
        </UnmetConstraintsTooltip>

        <ButtonNext
          loading={deleting}
          color='destructive'
          onClick={handleDelete}
          content='text'
          size='regular'
          variant='tertiary'
        >
          <FormattedMessage {...messages.deleteButton} />
        </ButtonNext>
      </FormWrapper>
    </Root>
  )
}

const Label = styled.label`
  ${styles.text.label}
  display: block;
  margin-bottom: ${styles.spacings['1x']};
`

const Root = styled.div`
  background-color: ${({ theme }) => theme.colors.surface_primary.regular};
  position: relative;
  height: auto;
  border-radius: 8px;
  padding: ${styles.spacings['5px']};
  width: 350px;
  box-shadow: 0 1px 3px rgba(128, 128, 128, 0.5);
`

const FormWrapper = styled.div`
  display: flex;
  padding: ${styles.spacings['3x']};
  flex-direction: column;
`
