export const breathe = keyframes`
  ${getAnimationValues().map(
    ([percentage, scale]) => `${percentage}% { transform: scale(${scale}); }`
  )}
`

function timerButtonAnimationCss({
  theme,
  isStop,
}: {
  theme
  isStop?: boolean
}) {
  return css`
    /* Tweakable animation properties */
    --button-hover-on-duration: 0.2s;
    --button-hover-off-duration: 0.25s;
    --button-breathe-normal-size: 1.3;
    --button-breathe-hover-size: 1.2; /* shrink a little when hover or focus */
    --button-breathe-shrunk-size: 1.1; /* the smallest size, when pressed in or when first becoming red */
    --button-breathe-expanded-size: 1.35; /* the largest size while breathing 'in' */
    --button-breathe-animation-duration: 4s; /* the total time to breathe in and out */
    --button-breathe-animation-delay: 0.5s;
    --button-breathe-opacity: 16%;

    position: relative;
    /* Stacking context for the :before element to be visually _behind_ the button */
    z-index: 0;

    transition: background-color 0.2s;

    /* The "halo" behind the button that has the breathing animation */
    &:before {
      display: block;
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;

      content: ' ';
      pointer-events: none;

      border-radius: inherit;
      background: ${theme.colors.accent.regular};
      opacity: var(--button-breathe-opacity);

      transform-origin: center center;
      transform: scale(var(--button-breathe-normal-size));
      transition: all var(--button-hover-off-duration) linear;

      ${isStop &&
      css`
        background: ${theme.colors.orange.regular};

        /* Begin shrunk after starting timer */
        transform: scale(var(--button-breathe-shrunk-size));

        /* Animate, slightly delayed */
        animation: ${breathe} var(--button-breathe-animation-duration) linear;
        animation-delay: var(--button-breathe-animation-delay);
        animation-iteration-count: infinite;

        @media (prefers-reduced-motion: reduce) {
          animation: none;
        }
      `};
    }

    &:not(:disabled) {
      &:hover,
      &:focus {
        svg rect:first-of-type {
          fill: ${isStop
            ? theme.colors.orange.hover
            : theme.colors.accent.hover};
        }

        ${!isStop &&
        css`
          /* Grow the halo when focused or hovered */
          :before {
            transform: scale(var(--button-breathe-hover-size));
            transition-duration: var(--button-hover-on-duration);
          }
        `};
      }

      /*  focus-visible is unsupported in Safari. Default focus
        rule defined above, focus-visible used below for browsers
        that support it (it'll be invalid in Safari and do nothing).
       */
      &:focus:not(:hover):not(:focus-visible) {
        svg rect:first-of-type {
          fill: ${isStop
            ? theme.colors.orange.regular
            : theme.colors.accent.regular};
        }
        :before {
          transform: scale(var(--button-breathe-normal-size));
        }
      }

      &:active {
        svg rect:first-of-type {
          fill: ${isStop
            ? theme.colors.orange.onClick
            : theme.colors.accent.onClick};
        }
      }
    }

    ${isStop &&
    css`
      /* Removing the animation and specifying transform gives us the illusion of
      a smoother transition back to the default halo state. Without this, the
      animation would appear to more abruptly stop, as we can't transition from
      an animation keyframe to the default transform. */
      &:active:before {
        animation: none;
        transform: scale(var(--button-breathe-hover-size));
      }
    `}
  `
}

// Please refer to https://www.notion.so/toggl/Breathing-formula-9d9bd61b2fd2485b9fab34524f3bebae
function getAnimationValues() {
  const min = 1.1
  const max = 1.35
  const diff = max - min

  function easeOutSine(x: number) {
    const easing = Math.sin((x * Math.PI) / 2)
    return min + easing * diff
  }
  function easeInCubic(x: number) {
    const easing = x * x * x
    return min + easing * diff
  }

  return [
    // Breathing in
    [5, easeOutSine(0.1)],
    [10, easeOutSine(0.2)],
    [15, easeOutSine(0.3)],
    [20, easeOutSine(0.4)],
    [25, easeOutSine(0.5)],
    [30, easeOutSine(0.6)],
    [35, easeOutSine(0.7)],
    [40, easeOutSine(0.8)],
    [45, easeOutSine(0.9)],
    [50, easeOutSine(1)],

    // Breathing out (easing is reversed)
    [55, easeInCubic(0.9)],
    [60, easeInCubic(0.8)],
    [65, easeInCubic(0.7)],
    [70, easeInCubic(0.6)],
    [75, easeInCubic(0.5)],
    [80, easeInCubic(0.4)],
    [85, easeInCubic(0.3)],
    [90, easeInCubic(0.2)],
    [95, easeInCubic(0.1)],
    [100, easeInCubic(0)],
  ]
}

