import { usePomodoroStorageState } from '@src/pomodoro/usePomodoroStorageState'
import * as React from 'react'

import { createContext } from '../utils/createContext'

export type PopupView = 'LIST' | 'POMODORO'

export type PopupContextReturn = {
  activeView: PopupView
  setActiveView: React.Dispatch<React.SetStateAction<PopupView>>
}

const [PopupContextProvider, usePopupContext] =
  createContext<PopupContextReturn>({
    strict: false,
    name: 'PopupContext',
  })

export const PopupProvider = ({
  children,
  ...rest
}: React.PropsWithChildren) => {
  const state = usePomodoroStorageState()
  const [activeView, setActiveView] = React.useState<PopupView>('LIST')

  React.useEffect(() => {
    if (state.computed.isRunningBreak || state.isRunning) {
      setActiveView('POMODORO')
    }
  }, [state.computed.isRunningBreak, state.isRunning])

  return (
    <PopupContextProvider value={{ activeView, setActiveView }} {...rest}>
      {children}
    </PopupContextProvider>
  )
}

export { usePopupContext }
