import styled from '@emotion/styled'
import { FormattedMessage, defineMessages } from 'react-intl'

const messages = defineMessages({
  today: {
    id: 'summary.today',
    defaultMessage: 'Today',
  },
  thisWeek: {
    id: 'summary.thisWeek',
    defaultMessage: 'Week total',
  },
})

function Summary({
  totals,
}: {
  totals: {
    today: string
    week: string
  }
}) {
  return (
    <SummaryRow>
      <div>
        <FormattedMessage {...messages.today} tagName={Label} />
        {totals.today}
      </div>
      <div>
        <FormattedMessage {...messages.thisWeek} tagName={Label} />
        {totals.week}
      </div>
    </SummaryRow>
  )
}

const SummaryRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 8px;

  background-color: ${({ theme }) => theme.colors.surface_secondary.regular};
  color: ${({ theme }) => theme.colors.ui.regular};
  font-weight: ${({ theme }) => theme.typography.fontWeight.bold};
  font-size: 11px;
`

const Label = styled.span`
  color: ${({ theme }) => theme.colors.ui_060.regular};
  text-transform: uppercase;
  margin-right: 8px;
`

export default Summary
