import { css } from '@emotion/react'
import styled from '@emotion/styled'
import { WarningIcon } from '@src/ui/icons/WarningIcon'
import type * as React from 'react'
import {
  FormattedListParts,
  FormattedMessage,
  defineMessages,
} from 'react-intl'

import styles from '@toggl/style'
import { Tooltip } from '@toggl/ui'

const messages = defineMessages({
  cta: {
    id: 'unmetConstraintsTooltip.cta',
    defaultMessage:
      'Fill out the required fields to stop the timer and save this time entry',
  },
  add: {
    id: 'unmetConstraintsTooltip.add',
    defaultMessage: 'Add ',
  },
  description: {
    id: 'unmetConstraintsTooltip.description',
    defaultMessage: 'Description',
  },
  tag: {
    id: 'unmetConstraintsTooltip.tag',
    defaultMessage: 'Tag',
  },
  task: {
    id: 'unmetConstraintsTooltip.task',
    defaultMessage: 'Task',
  },
  project: {
    id: 'unmetConstraintsTooltip.project',
    defaultMessage: 'Project',
  },
})

export const UnmetConstraintsTooltip: React.FC<{
  children: React.ReactElement
  missingProps: Array<string>
  onClick?: React.MouseEventHandler
  placement?: 'top' | 'right' | 'bottom' | 'left' | undefined
  enabled?: boolean
}> = (props) => {
  return (
    <Tooltip enabled={props.enabled} asChild delayDuration={0}>
      {props.children}
      <Tooltip.Content
        side={props.placement || 'bottom'}
        onClick={props.onClick}
      >
        <div
          css={css`
            max-width: 300px;
          `}
        >
          <Header missingProps={props.missingProps} />
          <FormattedMessage tagName={TooltipBody} {...messages.cta} />
        </div>
      </Tooltip.Content>
    </Tooltip>
  )
}

const Header: React.FC<{ missingProps: string[] }> = (props) => {
  return (
    <div
      css={css`
        ${styles.typography.h5Subtitle}
        display: flex;
        align-items: center;
      `}
    >
      <WarningIcon />
      <FormattedListParts type='conjunction' value={props.missingProps}>
        {(parts) => (
          <div
            css={css`
              margin-left: 8px;
            `}
          >
            <FormattedMessage {...messages.add} />
            {parts.map((part, index) => {
              return index % 2 === 0 ? (
                <FormattedMessage
                  {...messages[part.value]}
                  key={`${part.value}-${index}`}
                />
              ) : (
                <span key={`${part.value}-${index}`}>{part.value}</span>
              )
            })}
          </div>
        )}
      </FormattedListParts>
    </div>
  )
}

const TooltipBody = styled.div`
  ${styles.typography.p1Body}
  margin-top: ${styles.spacings['1x']};
  white-space: break-spaces;
`
