import { logger } from '@src/lib/errorReport'
import { type MessageFrom } from '@src/typdef'
import { useMutation } from '@tanstack/react-query'
import Browser from 'webextension-polyfill'

import { type TimeEntryRequest, type TimeEntryV9 } from '@toggl/track-types'

type DispatchContext = { entry: TimeEntryRequest; from: MessageFrom }
type MutationContext = { entry: TimeEntryRequest; integration: MessageFrom }

const StartTEService = {
  // WIP: Still using legacy event 'start-entry' to keep logic.
  dispatch(data: DispatchContext): Promise<TimeEntryV9> {
    return Browser.runtime.sendMessage({
      type: 'start-entry',
      payload: data,
    })
  },
}

type MutationData = TimeEntryV9

const useStartTEMutation = () => {
  return useMutation<MutationData, Error, MutationContext>(
    (data) =>
      StartTEService.dispatch({ entry: data.entry, from: data.integration }),
    {
      onError: (error) => {
        return logger.error(error)
      },
    }
  )
}

export { StartTEService, useStartTEMutation }
