import * as React from 'react'

export const AutoHeighter = ({
  children,
  scrollable,
  className,
  transitionDuration = 0.075,
}: {
  children: React.ReactNode
  scrollable?: boolean
  className?: string
  transitionDuration?: number
}) => {
  const ref = React.useRef<HTMLDivElement>(null)
  const height = useHeightObserver(ref)

  return (
    <div
      style={{
        height,
        overflow: scrollable ? 'auto' : 'hidden',
        transition: `height ${transitionDuration}s ease`,
        scrollbarGutter: scrollable ? 'stable' : undefined,
      }}
      className={className}
    >
      <div ref={ref}>{children}</div>
    </div>
  )
}

export const useHeightObserver = (ref: React.RefObject<HTMLDivElement>) => {
  const [heightVar, setHeightVar] = React.useState('')

  React.useEffect(() => {
    const refElement = ref.current

    const observer = new ResizeObserver((entries) => {
      const entry = entries[0]
      const newHeight = `${entry.contentRect.height}px`
      setHeightVar(newHeight)
    })

    if (refElement) {
      observer.observe(refElement)
    }

    return () => {
      if (refElement) {
        observer.unobserve(refElement)
      }
    }
  }, [ref])

  return heightVar
}
