import { useBrowserStorageLocal } from '@src/v4/lib/use-browser-storage'

import { initialState } from './config'
import { type PomodoroState } from './PomodoroService'

export const usePomodoroStorageState = () => {
  const [storage, , { isCacheLoading }] = useBrowserStorageLocal(
    'Pomodoro.State',
    initialState
  )

  return (isCacheLoading ? initialState : storage) as PomodoroState
}
