import styled from '@emotion/styled'
import { type Time } from '@internationalized/date'
import { TimeField } from '@src/ui/components/TimeField'
import moment from 'moment'
import type * as React from 'react'
import { FormattedDate, FormattedMessage, defineMessages } from 'react-intl'

type Props = {
  value?: Time
  date: moment.Moment
  onChange: (value: Time) => void
  onClick?: React.MouseEventHandler
}

const messages = defineMessages({
  today: {
    id: 'StartTimeInput.today',
    defaultMessage: 'Today',
  },
})

export const StartTimeInput: React.FC<Props> = (props) => {
  return (
    <Root
      onClick={(e) => {
        props.onClick && props.onClick(e)
      }}
    >
      <TimeField
        value={props.value}
        onChange={props.onChange}
        hourCycle={24}
        onClick={props.onClick}
      />
      <span>
        {moment(props.date).isSame(moment().valueOf(), 'd') ? (
          <FormattedMessage {...messages.today} />
        ) : (
          <FormattedDate
            value={props.date.toISOString()}
            day='2-digit'
            month='2-digit'
          />
        )}
      </span>
    </Root>
  )
}

const Root = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid ${({ theme }) => theme.colors.ui_040.regular};
  border-radius: 8px;

  span {
    color: ${({ theme }) => theme.colors.accent_120.regular};
    font-size: 14px;
    font-weight: 300;
    margin-right: 12px;
  }

  & > div {
    border: none;
  }
`
