import styled from '@emotion/styled'
import { AppThemeProvider } from '@src/components/AppThemeProvider'
import { Overlay } from '@src/components/Overlay'
import { useSelectedWorkspaceIdStorage } from '@src/v4/services/SelectedWorkspaceService'
import { useUserStorage } from '@src/v4/services/UsersService'
import { useWorkspacesListStorage } from '@src/v4/services/WorkspacesListService'
import * as React from 'react'

import styles from '@toggl/style'

import { WorkspacePopdownContent } from './WorkspacePopdownContent'
import { WorkSpaceTrigger } from './WorkSpaceTrigger'

export function WorkspacePopdown() {
  const [isOpen, setIsOpen] = React.useState(false)
  const [workspaceMap] = useWorkspacesListStorage()
  const [user] = useUserStorage()

  const [selectedWSId] = useSelectedWorkspaceIdStorage()

  const selectedWS = workspaceMap?.[selectedWSId ?? '']
  if (!user) return null
  return (
    <PopdownAnchor>
      <WorkSpaceTrigger
        workspaceName={selectedWS?.name ?? 'Loading ...'}
        isOpen={isOpen}
        onOpenChange={() => setIsOpen((open) => !open)}
      />
      {isOpen && (
        <AppThemeProvider>
          <WorkspacePopdownContentStyled workspaceMap={workspaceMap} />
        </AppThemeProvider>
      )}
      <Overlay open={isOpen} onClick={() => setIsOpen((open) => !open)} />
    </PopdownAnchor>
  )
}

const PopdownAnchor = styled.div`
  position: relative;
`

const WorkspacePopdownContentStyled = styled(WorkspacePopdownContent)`
  position: absolute;
  top: 40px;
  left: -30px;
  z-index: 11;
  width: 425px;
  max-height: 400px;
  border-radius: ${styles.radii.regular};
`
