import { useWebWorker } from '@src/lib/hooks/useWebWorker'
import { type TagsPopdownFilterWorkerResponse } from '@src/workers/types'
import * as React from 'react'

import { type Tag } from '@toggl/track-types'

type Props = {
  selectedIds: number[]
  wid?: number | null
  tagsMap?: Record<string, Tag> | null
  open?: boolean
}

export function useTagsPopdownFilter(filter: string, props: Props) {
  const [tags, setTags] = React.useState<Tag[]>([])
  const [filteredTagExists, setFilteredTagExists] =
    React.useState<boolean>(false)
  const [selectedIdsMap, setSelectedIdsMap] = React.useState<{
    [p: number]: boolean
  }>({})

  const onWorkerResponse = React.useCallback(
    (data: MessageEvent<TagsPopdownFilterWorkerResponse>) => {
      const { filteredTags, filteredTagExists } = data.data
      setTags(filteredTags)
      setFilteredTagExists(filteredTagExists)
    },
    [setTags, setFilteredTagExists]
  )
  const { postMessage } = useWebWorker(
    'src/workers/tagsPopdownFilter.worker.js',
    onWorkerResponse
  )

  React.useEffect(() => {
    const selectedIdsMap = props.selectedIds.reduce((map, id) => {
      map[id] = true
      return map
    }, {} as { [key: number]: boolean })
    setSelectedIdsMap(selectedIdsMap)
  }, [props.selectedIds])

  React.useEffect(() => {
    postMessage({
      selectedTagIds: props.selectedIds,
      wid: props.wid,
      filter,
      tags: props.tagsMap,
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filter, props.tagsMap, props.wid, postMessage, props.open])

  return React.useMemo(
    () => ({
      tags,
      filteredTagExists,
      selectedIdsMap,
    }),
    [tags, filteredTagExists, selectedIdsMap]
  )
}
