import { type ExtensionContextValue, type ViewType } from '@src/context'
import { useUserStorage } from '@src/v4/services/UsersService'
import * as React from 'react'

import { type TimeEntryV9 } from '@toggl/track-types'

export default function useContextState(): ExtensionContextValue {
  const [user, , { isCacheLoading: isLoadingUser }] = useUserStorage()

  const [view, changeView] = React.useState<ViewType>('default')

  const [editingEntry, setEditingEntry] = React.useState<
    TimeEntryV9 | undefined
  >(undefined)

  const changeEditingEntry = (entry: TimeEntryV9 | undefined) => {
    setEditingEntry(entry)
  }

  return {
    loading: isLoadingUser,
    view,
    user,
    editingEntry,
    changeView,
    changeEditingEntry,
  }
}
