import { useExtensionContext } from '@src/context'
import { CircularTimer } from '@src/pomodoro/components/CircularTimer'
import { usePomodoroInterval } from '@src/pomodoro/usePomodoroInterval'
import { usePomodoroMessageBus } from '@src/pomodoro/usePomodoroMessageBus'
import { usePomodoroSettings } from '@src/pomodoro/usePomodoroSettings'
import { usePomodoroStorageState } from '@src/pomodoro/usePomodoroStorageState'
import { getActionLabel } from '@src/pomodoro/utils/getActionLabel'
import { getFormattedCountdown } from '@src/pomodoro/utils/getFormattedCountdown'
import { getPomodoroColors } from '@src/pomodoro/utils/getPomodoroColors'
import { getPomodoroLabel } from '@src/pomodoro/utils/getPomodoroLabel'
import { Box } from '@src/shared/components/Box'
import { Flex } from '@src/shared/components/Flex'
import { useCurrentTimeEntryStorage } from '@src/v4/services/CurrentTEService'
import { useStartTEMutation } from '@src/v4/services/StartTEService'
import { useUpdateTEMutation } from '@src/v4/services/UpdateTEService'
import Browser from 'webextension-polyfill'

import styles from '@toggl/style'
import { ButtonNext } from '@toggl/ui'

export default function Pomodoro() {
  const { user } = useExtensionContext()
  const state = usePomodoroStorageState()
  const actions = usePomodoroMessageBus()
  const remaining = usePomodoroInterval(state)
  const startTEMutation = useStartTEMutation()
  const updateTEMutation = useUpdateTEMutation()
  const [current] = useCurrentTimeEntryStorage()
  const [settings] = usePomodoroSettings()

  const onClickSettings = () => {
    Browser.tabs.create({
      url: Browser.runtime.getURL('settings.html#/pomodoro'),
    })
  }

  const onClickAction = () => {
    if (state.computed.isIdle) {
      startTEMutation.mutate({
        entry: {
          at: new Date().toISOString(),
          billable: false,
          description: '',
          duration: -1,
          start: new Date().toISOString(),
          tags: [],
          tag_ids: [],
          workspace_id: user?.default_workspace_id ?? 0,
          created_with: '',
        },
        integration: { type: 'popup' },
      })
    }

    if (state.computed.isIdleBreak) {
      actions.start()
    }

    if (state.computed.isRunningBreak) {
      actions.goToNextState()
    }

    if (state.computed.isRunningFocus) {
      if (!current) {
        return false
      }

      const stopTime = new Date()
      const startTime = new Date(current.start)
      const duration = Math.floor(
        (stopTime.getTime() - startTime.getTime()) / 1000
      )

      updateTEMutation.mutate({
        entry: {
          ...current,
          stop: stopTime.toISOString(),
          duration,
        },
        integration: { type: 'popup' },
      })
    }
  }

  // Calculate Progress
  const progress = remaining < 1 ? 1 : (remaining / state.duration) * 100

  // Display Label
  const label = getPomodoroLabel(state)

  // Calculate Colors
  const colors = getPomodoroColors(state)

  // Action Label
  const actionLabel = getActionLabel(state)

  return (
    <Box
      css={{
        flex: 1,
        overflowY: 'auto',
        backgroundColor: styles.colors.surface_secondary.regular,
      }}
    >
      <Box
        css={{
          margin: styles.spacings['2x'],
          padding: `${styles.spacings['2x']} 0px`,
          borderRadius: 8,
          backgroundColor: colors.background,
        }}
      >
        <Box css={{ padding: `${styles.spacings['1.5x']} 118px` }}>
          <CircularTimer
            progress={progress}
            label={`${label} ${state.session}/${settings?.sessions}`}
            opts={{
              color: colors.arc,
            }}
          >
            {getFormattedCountdown(remaining)}
          </CircularTimer>
        </Box>

        <Flex
          css={{
            flexDirection: 'column',
            gap: styles.spacings['2x'],
            margin: `${styles.spacings['2x']} ${styles.spacings['4x']}`,
          }}
        >
          <ButtonNext
            color='regular'
            content='text'
            size='regular'
            variant='primary'
            css={{
              width: '100%',
              backgroundColor: colors.arc,
              '&:hover': {
                backgroundColor: colors.arc,
              },
            }}
            onClick={onClickAction}
            disabled={startTEMutation.isLoading || updateTEMutation.isLoading}
          >
            {actionLabel}
          </ButtonNext>

          <ButtonNext
            content='text'
            size='regular'
            variant='tertiary'
            onClick={onClickSettings}
            css={{ width: '100%' }}
          >
            {'Pomodoro Settings'}
          </ButtonNext>
        </Flex>
      </Box>
    </Box>
  )
}
