import styled from '@emotion/styled'
import { useSelectedWorkspaceIdStorage } from '@src/v4/services/SelectedWorkspaceService'
import * as React from 'react'
import { FormattedMessage, defineMessages } from 'react-intl'

import styles from '@toggl/style'
import { type Organization, type Workspace } from '@toggl/track-types'
import { CheckmarkIconNext } from '@toggl/ui'

type Props = {
  organizations: (Organization & { workspaces: Workspace[] })[]
  filter: string
}

const messages = defineMessages({
  noMatchingWorkspaces: {
    id: 'OrganizationWorkspaceMenu.noMatchingWorkspaces',
    defaultMessage: 'No matching workspaces or organizations',
  },
})

export function FilteredWorkspaceList({ organizations, filter }: Props) {
  const [selectedWsId, setSelectedWS] = useSelectedWorkspaceIdStorage()
  const filteredOrganizations = React.useMemo(() => {
    return organizations.map((org) => ({
      ...org,
      workspaces: org.name.toLowerCase().includes(filter.toLowerCase())
        ? org.workspaces
        : org.workspaces.filter((ws) =>
            ws.name.toLowerCase().includes(filter.toLowerCase())
          ),
    }))
  }, [organizations, filter])

  return (
    <FilterList>
      {filteredOrganizations?.map((organization) => (
        <React.Fragment key={organization.id}>
          {organization.workspaces.map((workspace) => (
            <FilterItem
              key={workspace.id}
              data-selected={selectedWsId === workspace.id}
              onClick={() => setSelectedWS(workspace.id)}
            >
              <p>
                <span>{organization.name}</span>
                <span>
                  {`  /  `}
                  {workspace.name}
                </span>
              </p>
              <StyledCheckMark
                data-selected={selectedWsId === workspace.id}
                size='2x'
                color='accent'
              />
            </FilterItem>
          ))}
        </React.Fragment>
      ))}
      <FilterEmptyState>
        <FormattedMessage {...messages.noMatchingWorkspaces} />
      </FilterEmptyState>
    </FilterList>
  )
}

const FilterList = styled.ul`
  padding: 0 ${styles.spacings['2x']} ${styles.spacings['1x']};
  overflow: auto;
`

const FilterItem = styled.button`
  ${styles.unset.button};
  ${styles.text.nextP1Body};
  padding: 0 ${styles.spacings['2x']};
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  border-radius: ${styles.radii.regular};

  transition: background-color 0.15s ease-in;

  font-weight: ${styles.text.fontWeight.normal};

  &[data-selected='true'] {
    font-weight: ${styles.text.fontWeight.medium};
  }

  &:hover,
  &:focus-visible {
    transition: background-color 0.1s ease-out;
    background-color: ${styles.colors.surface_tertiary.hover};
  }

  p span:first-of-type {
    color: ${styles.colors.ui_060.regular};
  }
`

const FilterEmptyState = styled.div`
  padding: ${styles.spacings['2x']};

  display: none;

  &:only-child {
    display: block;
  }
`

const StyledCheckMark = styled(CheckmarkIconNext)`
  opacity: 0;
  filter: blur(2px);
  transition: opacity 0.15s ease-in, filter 0.15s ease-in;

  &[data-selected='true'] {
    opacity: 1;
    filter: blur(0);
    transition: opacity 0.25s ease-out, filter 0.25s ease-out;
  }
`
