import { CustomDropdownTrigger } from '@src/components/timer/styles'

import { BillableIconNext } from '@toggl/ui'

type Props = {
  billable?: boolean
  onChange: (billable?: boolean) => void
}

export function BillableTrigger(props: Props) {
  return (
    <CustomDropdownTrigger
      highlight={props.billable}
      onClick={() => props.onChange(!props.billable)}
      data-heap-id='extension:mark-billable'
    >
      <BillableIconNext color='ui_050' size='2x' />
    </CustomDropdownTrigger>
  )
}
